import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ConversationComponent } from '../conversation/conversation.component';
import { Message } from '../conversation/message/message';
import * as jQuery from 'jquery';
import { MessageService } from '../shared/services/message.service';

@Component({
  selector: 'app-conversation-page',
  templateUrl: './conversation-page.component.html',
  styleUrls: ['./conversation-page.component.scss']
})
export class ConversationPageComponent implements OnInit {
    processing = false;
  ournumber = '';
  theirnumber = '';
  showConversation = false;
  @ViewChild(ConversationComponent) ConversationComponent: ConversationComponent;
  constructor(
    private route: ActivatedRoute,
    private messageService: MessageService
  ) {
    this.route.params.subscribe(() => {

      this.showConversation = false;
      this.ournumber = this.route.snapshot.params['ournumber'];
      this.theirnumber = this.route.snapshot.params['theirnumber'];
      setTimeout(() => {
        this.showConversation = true;
      }, 100);
    });
  }

  ngOnInit() {
  }

  onMessageSend(msg: Message) {
    this.ConversationComponent.messages.push(msg);
    this.scrollToLatest();
  }

  scrollToLatest() {
    jQuery('#conversation').animate({ scrollTop: jQuery('#conversationContent').height() }, 500, 'swing');
  }

  afterConversationLoad() {

    setTimeout(() => {
      this.scrollToLatest();
      this.processing = false;
    }, 600);
  }

  onFromShow() {
    setTimeout(() => {
      this.scrollToLatest();
    }, 100);
  }

    onConversationLoadStart(){
    this.processing = true;
  }

}



