import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NumberThreadsComponent } from '../number-threads/number-threads.component';
import { UserService } from '../shared/services/user.service';

@Component({
  selector: 'app-show-number-threads',
  templateUrl: './show-number-threads.component.html',
  styleUrls: ['./show-number-threads.component.scss']
})
export class ShowNumberThreadsComponent implements  AfterViewInit {
  number: any;
  page = 1;
  componentReady = false;
    @ViewChild(NumberThreadsComponent) NumberThreadsComponent;
    public showNumberThreads: boolean =  true;
  constructor(
      private activatedRoute: ActivatedRoute,
      private userService: UserService
  ) {
    this.init();
  }

  init(){

      this.activatedRoute.params.subscribe(() => {
          const page = this.activatedRoute.snapshot.params['page'];
          this.number = this.getNumberObj(this.activatedRoute.snapshot.params['number']);
          this.page = page ? page : this.page;
      });
  }

  ngAfterViewInit() {

      this.componentReady = true;
  }

  getNumberObj(num){
      // setting default value
      let matchedNum = {number: num, contactId: null, contactName: null};
      this.userService.user.numbers.forEach((numberObj: any) => {

          if(num==numberObj.number){
              matchedNum = numberObj;
          }

      });

      return matchedNum;
  }

}



