import { Component, Inject, OnInit, Optional, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material';
import { GeneralService } from '../services/general.service';

@Component({
  templateUrl: './date-time-picker-modal.component.html',
  styleUrls: ['./date-time-picker-modal.component.scss']
})
export class DateTimePickerModalComponent implements OnInit {
  times = [];
  date;
  time;
  minDate = new Date();
  @ViewChild('matDatepickerInput') datePickerInput;
  constructor(
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any,
    private generalService: GeneralService
  ) {
    this.generateTimes();
  }

  ngOnInit() {
  }

  shouldDisableSaveBtn() {
    return !this.date || !this.time;
  }

  dateChanged(dateObject) {
    console.log(this.date);
    const date = dateObject.toDateString();
    this.generateTimes(date);
  }

  generateTimes(selectedDate = '') {


    const times = [

      { value: '00:30:00', label: '12:30 am' },
      { value: '01:00:00', label: '01:00 am' },
      { value: '01:30:00', label: '01:30 am' },
      { value: '02:00:00', label: '02:00 am' },
      { value: '02:30:00', label: '02:30 am' },
      { value: '03:00:00', label: '03:00 am' },
      { value: '03:30:00', label: '03:30 am' },
      { value: '04:00:00', label: '04:00 am' },
      { value: '04:30:00', label: '04:30 am' },
      { value: '05:00:00', label: '05:00 am' },
      { value: '05:30:00', label: '05:30 am' },
      { value: '06:00:00', label: '06:00 am' },
      { value: '06:30:00', label: '06:30 am' },
      { value: '07:00:00', label: '07:00 am' },
      { value: '07:30:00', label: '07:30 am' },
      { value: '08:00:00', label: '08:00 am' },
      { value: '08:30:00', label: '08:30 am' },
      { value: '09:00:00', label: '09:00 am' },
      { value: '09:30:00', label: '09:30 am' },
      { value: '10:00:00', label: '10:00 am' },
      { value: '10:30:00', label: '10:30 am' },
      { value: '11:00:00', label: '11:00 am' },
      { value: '11:30:00', label: '11:30 am' },
      { value: '12:00:00', label: '12:00 pm' },
      { value: '12:30:00', label: '12:30 pm' },
      { value: '13:00:00', label: '01:00 pm' },
      { value: '13:30:00', label: '01:30 pm' },
      { value: '14:00:00', label: '02:00 pm' },
      { value: '14:30:00', label: '02:30 pm' },
      { value: '15:00:00', label: '03:00 pm' },
      { value: '15:30:00', label: '03:30 pm' },
      { value: '16:00:00', label: '04:00 pm' },
      { value: '16:30:00', label: '04:30 pm' },
      { value: '17:00:00', label: '05:00 pm' },
      { value: '17:30:00', label: '05:30 pm' },
      { value: '18:00:00', label: '06:00 pm' },
      { value: '18:30:00', label: '06:30 pm' },
      { value: '19:00:00', label: '07:00 pm' },
      { value: '19:30:00', label: '07:30 pm' },
      { value: '20:00:00', label: '08:00 pm' },
      { value: '20:30:00', label: '08:30 pm' },
      { value: '21:00:00', label: '09:00 pm' },
      { value: '21:30:00', label: '09:30 pm' },
      { value: '22:00:00', label: '10:00 pm' },
      { value: '22:30:00', label: '10:30 pm' },
      { value: '23:00:00', label: '11:00 pm' },
      { value: '23:30:00', label: '11:30 pm' },
      { value: '24:00:00', label: '12:00 am' },

    ];
    var isToday = selectedDate == new Date().toDateString() //determines if the selected date is today

    this.times = isToday ? this.excludePastTimes(times, selectedDate) : times; //If it is today it limits the times
  }

  excludePastTimes(times, selectedDateString) {
    const d = new Date();
    const currentDateString = d.toDateString();

    const currentTime = d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds();
    const currentTimeInSeconds = this.generalService.convertTimeToSeconds(currentTime);
    times = times.filter((time, index) => {
      const timeInSeconds = this.generalService.convertTimeToSeconds(time.value);

      console.log(time, timeInSeconds);

      return (timeInSeconds > currentTimeInSeconds && timeInSeconds !== 86400) ||
        (currentDateString && currentDateString != selectedDateString && timeInSeconds === 86400);
      // return (timeInSeconds > currentTimeInSeconds && timeInSeconds !== 86400) ||
      //     (currentDateString && currentDateString != selectedDateString && timeInSeconds === 86400);
    });
    return times;
  }

}



