import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { APPCONFIG } from './config';
import { LayoutService } from './layout/layout.service';
import { StripeComponent } from './stripe/stripe.component';

// 3rd
import 'styles/material2-theme.scss';
import 'styles/bootstrap.scss';
// custom
import 'styles/layout.scss';
import 'styles/theme.scss';
import 'styles/ui.scss';
import 'styles/app.scss';
import { UserService } from './shared/services/user.service';
import {GooglePickerService} from './shared/services/google-picker.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
  providers: [LayoutService],
  // directives: [StripeComponent],
})
export class AppComponent implements OnInit {
  public AppConfig: any;
  constructor(
      private router: Router,
      public userService: UserService,
      // initialize Google Picker to utilize later
      private googlePicker: GooglePickerService
  ) { }

  ngOnInit() {
    this.AppConfig = APPCONFIG;

    // Scroll to top on route change
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationStart)) {
        return;
      }

      this.onNavigationStart(evt);
    });
  }

  onNavigationStart(evt){
      this.checkIfUserIsLoggedIn(evt);
  }

  checkIfUserIsLoggedIn(evt){

      // check if user is logged In
      if (
          !this.userService.user &&
          evt instanceof NavigationStart &&
          evt.url != '/login' && evt.url != '/signUp'
      )
      {
          this.router.navigateByUrl('/login');
      }
  }
}



