import { NgModule } from '@angular/core';

import { EChartsDirective } from './echarts.directive';
import { SlimScrollDirective } from './slim-scroll.directive';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import {
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule, MatCardModule, MatCheckboxModule, MatChipsModule,
    MatDatepickerModule,
    MatDialogModule, MatExpansionModule,
    MatGridListModule, MatIconModule, MatInputModule, MatListModule, MatMenuModule, MatNativeDateModule,
    MatProgressBarModule, MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule, MatSidenavModule, MatSlideToggleModule,
    MatSnackBarModule, MatTabsModule,
    MatToolbarModule, MatTooltipModule
} from '@angular/material';
import { AvatarModule } from 'ngx-avatar';
import { ObjectToArrayPipe } from './pipes/object-to-array.pipe';
import { ContextMenuComponent } from './context-menu/context-menu.component';
import { FlipableAvatarComponent } from './flipable-avatar/flipable-avatar.component';
import { ContactCardLayoutComponent } from '../contacts/contact-card-layout/contact-card-layout.component';
import { RouterModule } from '@angular/router';
import { TagInputModule } from 'ngx-chips';

@NgModule({
    imports: [
        ReactiveFormsModule,
        FormsModule,
        CommonModule,
        MatButtonModule,
        MatButtonToggleModule,
        MatCardModule,
        MatCheckboxModule,
        MatChipsModule,
        MatDatepickerModule,
        MatDialogModule,
        MatExpansionModule,
        MatGridListModule,
        MatIconModule,
        MatInputModule,
        MatListModule,
        MatMenuModule,
        MatNativeDateModule,
        MatProgressBarModule,
        MatProgressSpinnerModule,
        MatRadioModule,
        MatRippleModule,
        MatSelectModule,
        MatSidenavModule,
        MatSnackBarModule,
        MatToolbarModule,
        MatTooltipModule,
        AvatarModule,
        MatAutocompleteModule,
        MatTabsModule,
        RouterModule,
        TagInputModule,
        MatSlideToggleModule
    ],
    declarations: [
        EChartsDirective,
        SlimScrollDirective,
        ObjectToArrayPipe,
        ContextMenuComponent,
        FlipableAvatarComponent,
        ContactCardLayoutComponent,
    ],
    exports: [
        MatTabsModule,
        FlipableAvatarComponent,
        EChartsDirective,
        SlimScrollDirective,
        ReactiveFormsModule,
        FormsModule,
        MatButtonModule,
        MatButtonToggleModule,
        MatCardModule,
        MatCheckboxModule,
        MatChipsModule,
        MatDatepickerModule,
        MatDialogModule,
        MatExpansionModule,
        MatGridListModule,
        MatIconModule,
        MatInputModule,
        MatListModule,
        MatMenuModule,
        MatNativeDateModule,
        MatProgressBarModule,
        MatProgressSpinnerModule,
        MatRadioModule,
        MatRippleModule,
        MatSelectModule,
        MatSidenavModule,
        MatSnackBarModule,
        MatToolbarModule,
        MatTooltipModule,
        MatAutocompleteModule,
        AvatarModule,
        ObjectToArrayPipe,
        ContextMenuComponent,
        ContactCardLayoutComponent,
        TagInputModule,
        MatSlideToggleModule
    ]
})

export class SharedModule {
}



