import { Component, OnInit, Input } from '@angular/core';
import { Thread } from '../thread';
import { MessageService } from '../../../../shared/services/message.service';
import { TimerService } from '../../../../shared/services/timer.service';
import { WebsocketService } from '../../../../shared/services/websocket.service';
import { AfterViewInit, AfterContentInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-thread-footer',
  templateUrl: './thread-footer.component.html',
  styleUrls: ['./thread-footer.component.scss']
})
export class ThreadFooterComponent implements AfterContentInit {

  @Input('thread') thread: Thread;
  displayTime: string = '';
  oneSecondTimerSubscription: Subscription;

  constructor(
    private messageService: MessageService,
    private timerService: TimerService
  ) {


  }

  ngAfterContentInit() {
    if (this.oneSecondTimerSubscription) this.oneSecondTimerSubscription.unsubscribe();
    this.oneSecondTimerSubscription = this.timerService.oneSecondTimer.subscribe(() => { this.updateTime(); });
    this.updateTime();
  }

  updateTime() {
    this.displayTime = this.messageService.showTime(this.thread, this.thread.status == 'scheduled' ? 'future' : '');
  }



  getColor(status) {
    switch (status) {
      case 'received':
        return 'badge-secondary'
      case 'delivered':
        return 'badge-success'
      case 'queued':
        return 'badge-warning'
      case 'scheduled':
        return 'badge-secondary'
      case 'failed':
        return 'badge-danger'
    }
    return 'badge-primary'
  }
}



