import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'app-flipable-avatar',
    templateUrl: './flipable-avatar.component.html',
    styleUrls: ['./flipable-avatar.component.scss']
})
export class FlipableAvatarComponent implements OnInit {

    @Output('addBtnClick') addBtnClick = new EventEmitter();
    @Output('editClick') editClick = new EventEmitter();
    @Output('avatarClick') avatarClick = new EventEmitter();
    @Input('gravatarId') gravatarId;
    @Input('name') name;
    @Input('active') active: boolean;
    @Input('disableEditIcon') disableEditIcon: boolean;
    isMouseOver = false;

    constructor() {
    }

    ngOnInit() {
    }

    onEditClick() {
        this.editClick.emit();
    }

    onAvatarClick() {
        alert(22);
        this.avatarClick.emit();
    }

    onAddBtnClick(){
        this.addBtnClick.emit();
    }

}



