import { NgModule, ApplicationRef } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ScrollToModule } from 'ng2-scroll-to';
import { MatDialogModule, MatCardModule, MatButtonModule } from '@angular/material';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
// Layout
import { LayoutComponent } from './layout/layout.component';
import { PreloaderDirective } from './layout/preloader.directive';
// Header
import { AppHeaderComponent } from './layout/header/header.component';
// Sidenav
import { AppSidenavComponent } from './layout/sidenav/sidenav.component';
import { ToggleOffcanvasNavDirective } from './layout/sidenav/toggle-offcanvas-nav.directive';
import { AutoCloseMobileNavDirective } from './layout/sidenav/auto-close-mobile-nav.directive';
import { AppSidenavMenuComponent } from './layout/sidenav/sidenav-menu/sidenav-menu.component';
import { AccordionNavDirective } from './layout/sidenav/sidenav-menu/accordion-nav.directive';
import { AppendSubmenuIconDirective } from './layout/sidenav/sidenav-menu/append-submenu-icon.directive';
import { HighlightActiveItemsDirective } from './layout/sidenav/sidenav-menu/highlight-active-items.directive';
// Footer
import { AppFooterComponent } from './layout/footer/footer.component';

// Sub modules
import { LayoutModule } from './layout/layout.module';
import { SharedModule } from './shared/shared.module';

// hmr
import { removeNgStyles, createNewHosts } from '@angularclass/hmr';
import { UserService } from './shared/services/user.service';
import { PageLoginComponent } from './login/login.component';
import { GeneralService } from './shared/services/general.service';
import { HttpClientModule } from '@angular/common/http';
import { NumberThreadsComponent } from './number-threads/number-threads.component';
import { ShowNumberThreadsComponent } from './show-number-threads/show-number-threads.component';
import { MessageService } from './shared/services/message.service';

import { MessagesComponent } from './messages/messages.component';
import { ConversationComponent } from './conversation/conversation.component';
import { ConversationPageComponent } from './conversation-page/conversation-page.component';
import { MessageComponent } from './conversation/message/message.component';
import { SendMessageFormComponent } from './conversation-page/send-message-form/send-message-form.component';
import { NewMessageWindowModule } from './new-message-window/new-message-window.module';
import { WebsocketService } from './shared/services/websocket.service';
import { ContactsComponent } from './contacts/contacts.component';
import { AddContactComponent } from './contacts/add-contact/add-contact.component';
import { ContactsService } from './shared/services/contacts.service';
import { NumberService } from './shared/services/number.service';

import { GooglePickerService } from './shared/services/google-picker.service';
import { ImageViewerModule } from './shared/image-viewer/image-viewer.module';
import { DateTimePickerModalComponent } from './shared/date-time-picker-modal/date-time-picker-modal.component';
import { MessageFormFloatingButtonsModule } from './shared/message-form-floating-buttons/message-form-floating-buttons.module';
import { SignUpComponent } from './sign-up/sign-up.component';
import { AppNavbarComponent } from './app-navbar/app-navbar.component';
import { LoginPopupComponent } from './login/login-popup/login-popup.component';
import { ApiService } from './shared/services/api.service';
import { TimerService } from './shared/services/timer.service';
import { TagsService } from './shared/services/tags.service';
import { WindowRef } from './shared/services/window-ref.service';
import { NumbersComponent } from './numbers/numbers.component';
import { NumberlistCardComponent } from './numbers/numberlist-card/numberlist-card.component';
import { NewNumberComponent } from './numbers/new-number/new-number.component';
import { DeleteNumberWarningModalComponent } from './numbers/delete-number-warning-modal/delete-number-warning-modal.component';
import { HostedNumberComponent } from './numbers/hosted-number/hosted-number.component';
import { VerificationCallWarningModalComponent } from './numbers/verification-call-warning-modal/verification-call-warning-modal.component';
import { ThreadModule } from './number-threads/thread.module/thread.module';
import { DialogComponent } from './shared/dialog/dialog.component';
import { ContactComponent } from './contacts/contact/contact.component';
import { CreateTagsDialogComponent } from './shared/create-tags-dialog/create-tags-dialog.component';
import { StripeComponent } from './stripe/stripe.component';
import { StripeFormComponent } from './stripe/stripe-form/stripe-form.component';
import { NumberThreadsContextMenuComponent } from './number-threads/number-threads-context-menu/number-threads-context-menu.component';
import { NotificationsComponent } from './notifications/notifications.component';
import { NotificationComponent } from './notifications/notification/notification.component';
import { ConfirmationModalComponent } from './numbers/confirmation-modal/confirmation-modal.component';
import { AdminGuard } from './admin/admin.guard';
import { NeedlesGuard } from './needles/needles.guard';

@NgModule({
    providers: [
        UserService,
        GeneralService,
        MessageService,
        WebsocketService,
        ContactsService,
        NumberService,
        GooglePickerService,
        ApiService,
        TimerService,
        TagsService,
        NumberService,
        WindowRef,

        // Guards
        AdminGuard,
        NeedlesGuard
    ],
    imports: [
        BrowserModule,
        HttpModule,
        HttpClientModule,
        FormsModule,
        BrowserAnimationsModule,
        MessageFormFloatingButtonsModule,
        AppRoutingModule,
        ScrollToModule.forRoot(),
        ImageViewerModule,
        // Sub modules
        LayoutModule,
        SharedModule,
        MatDialogModule,
        MatCardModule,
        MatButtonModule,
        ThreadModule,
        BrowserModule,
        HttpModule,
        HttpClientModule,
        FormsModule,
        BrowserAnimationsModule,
        MessageFormFloatingButtonsModule,
        AppRoutingModule,
        ScrollToModule.forRoot(),
        ImageViewerModule,
        // Sub modules
        LayoutModule,
        SharedModule,
        MatDialogModule,
        MatCardModule,
        MatButtonModule,
        NewMessageWindowModule,
        ThreadModule,
    ],
    declarations: [
        PageLoginComponent,
        AppComponent,
        // Layout
        LayoutComponent,
        PreloaderDirective,
        // Header
        AppHeaderComponent,
        // Sidenav
        AppSidenavComponent,
        ToggleOffcanvasNavDirective,
        AutoCloseMobileNavDirective,
        AppSidenavMenuComponent,
        AccordionNavDirective,
        AppendSubmenuIconDirective,
        HighlightActiveItemsDirective,
        // Footer
        AppFooterComponent,
        NumberThreadsComponent,
        NumberThreadsContextMenuComponent,
        ShowNumberThreadsComponent,
        MessagesComponent,
        ConversationComponent,
        ConversationPageComponent,
        MessageComponent,
        SendMessageFormComponent,
        ContactsComponent,
        ContactComponent,
        AddContactComponent,
        DateTimePickerModalComponent,
        SignUpComponent,
        AppNavbarComponent,
        LoginPopupComponent,
        DeleteNumberWarningModalComponent,
        VerificationCallWarningModalComponent,
        DialogComponent,
        ContactComponent,
        CreateTagsDialogComponent,
        NotificationsComponent,
        NotificationComponent,
        ConfirmationModalComponent
    ],
    bootstrap: [AppComponent],
    entryComponents: [
        DateTimePickerModalComponent,
        DeleteNumberWarningModalComponent,
        VerificationCallWarningModalComponent,
        DialogComponent,
        CreateTagsDialogComponent,
        ConfirmationModalComponent
    ]
})

export class AppModule {
    constructor(public appRef: ApplicationRef) {
    }

    hmrOnInit(store) {
        console.log('HMR store', store);
    }

    hmrOnDestroy(store) {
        const cmpLocation = this.appRef.components.map((cmp) => cmp.location.nativeElement);
        // recreate elements
        store.disposeOldHosts = createNewHosts(cmpLocation);
        // remove styles
        removeNgStyles();
    }

    hmrAfterDestroy(store) {
        // display new elements
        store.disposeOldHosts();
        delete store.diWindowRefsposeOldHosts;
    }
}



