import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UserService } from '../shared/services/user.service';
import { Router } from '@angular/router';
import { ApiService } from '../shared/services/api.service'

@Component({
  selector: 'my-page-login',
  styleUrls: ['./login.component.scss'],
  templateUrl: './login.component.html'
})

export class PageLoginComponent {
  formGroup: FormGroup;
  processing = false;
  public addAccountLoaderVisualization: boolean = false;
  public signup: any;
  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private router: Router,
    private apiService: ApiService
  ) {
    this.setupForm();
    this.initSignupForm()
  }

  initSignupForm() {
    this.signup = {
      email: undefined,
      password: undefined,
      password2: undefined,
      name: undefined,
      company: undefined,
      agree: undefined
    };
  }

  signUpFunction() {
    if (!this.validateEmail()) {
      alert('The email is invalid')
      return
    };

    if (!this.isValid(this.signup.company)) {
      alert('Firm is required and must be more than 4 characters.')
      return
    }
    if (!this.isValid(this.signup.password)) {
      alert('Password is required and must be more than 4 characters.')
      return
    }
    if (!this.passwordsMatch()) {
      alert('Passwords do not match.')
      return
    }
    if (!this.signup.agree) {
      alert('You must agree to the Terms and Conditions')
      return
    }
    this.addAccountLoaderVisualization = true;

    this.userService.signUp(this.signup).subscribe((response: any) => {
      if (response.success === true) {
        this.userService.start(response)
        this.router.navigateByUrl('/');
        // this.initSignupForm() // previous logic
      }
      this.addAccountLoaderVisualization = false;
    }, (response: any) => {
      let error;
      this.addAccountLoaderVisualization = false;
      if (response.error.error.message) {
        error = response.error.error.message;
      } else {
        error = response.error.error;
      }
    })

  }

  validate(t) {
    return t ? "/assets/images/signupForm/check-clipart-6.png" : "/assets/images/signupForm/xmark.png"
  }
  isValid(p) {
    return p && p.length > 4
  }
  validateEmail() {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return this.signup.email && re.test(this.signup.email);
  }
  passwordsMatch() {
    return this.signup.password == this.signup.password2 && this.isValid(this.signup.password)
  }

  setupForm() {
    const controls: any = {};
    controls.email = this.fb.control('', Validators.required);
    controls.password = this.fb.control('', Validators.required);
    this.formGroup = this.fb.group(controls);
  }

  login() {
    this.processing = true;
    this.apiService.call('/login', { email: this.formGroup.value.email, password: this.formGroup.value.password }).subscribe((response) => {
      if (response.success == true) {
        this.router.navigateByUrl('/');
      } else {
        alert(response.message);
      }

      this.processing = false;
    }, (response: any) => {
      this.processing = false;
    });
  }

  shouldDisableSubmit() {
    if (!this.formGroup.valid || this.processing === true) {
      return true;
    }
    return false;
  }

  getSubmitBtnLabel() {
    if (this.processing === true) {
      return 'please wait...';
    }

    return 'Login';
  }
}



