import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { environment } from "../../../environments/environment";



@Injectable()
export class GeneralService {

    constructor(
        private snackBar: MatSnackBar
    ) { }


    showNotification(message, action) {
        this.snackBar.open(message, action, {
            duration: 2000,
        });
    }


    getObjectKeyByValue(object, value) {
        const keys = Object.keys(object).filter((key) => {
            if (object[key] == value) { return true; }
        });
        var key = keys[0];
        return key;
    }

    convertTimeToSeconds(hms: string) {
        const a = hms.split(':'); // split it at the colons

        // minutes are worth 60 seconds. Hours are worth 60 minutes.
        const seconds = (+a[0]) * 60 * 60 + (+a[1]) * 60 + (+a[2]);
        return seconds;
    }

    encodeQueryData(data) {

        const ret = [];
        for (const d in data) {
            ret.push(encodeURIComponent(d) + '=' + encodeURIComponent(data[d]));
        }

        return ret.join('&');

    }

    removeObjectFromArrayByKeyValue(array, key, value) {

        const index: number = this.getObjectIndexFromArrayByKeyValue(array, key, value);
        if (index !== -1) {
            array.splice(index, 1);
        }
    }

    getObjectIndexFromArrayByKeyValue(array, key, value){

        let index = -1;

        array.forEach((item: any, i) => {
            if(item[key] == value) {
                index = i;
            }
        });

        return index;

    }

}



