import { Component, OnInit, Input, Output } from '@angular/core'; import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UserService } from '../../shared/services/user.service';
import { Router } from '@angular/router';
import { ApiService } from '../../shared/services/api.service'

@Component({
  selector: 'app-login-popup',
  templateUrl: './login-popup.component.html',
  styleUrls: ['./login-popup.component.scss']
})
export class LoginPopupComponent implements OnInit {

  email;
  password;
  formGroup: FormGroup;
  processing = false;
  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private router: Router,
    private apiService: ApiService
  ) { }

  @Input() login: any;

  ngOnInit() {
    this.setupForm();
  }


  setupForm() {
    const controls: any = {};
    controls.email = this.fb.control('', Validators.required);
    controls.password = this.fb.control('', Validators.required);
    this.formGroup = this.fb.group(controls);
  }

  loginFunction() {
    console.log(this.formGroup.value)
    this.processing = true;
    this.apiService.call('/login', { email: this.formGroup.value.email, password: this.formGroup.value.password }).subscribe((response) => {
      console.log('response:', response)
      if (response.success == true) {
        this.userService.start(response)
        this.router.navigateByUrl('/');
      } else {
        alert(response.message);

      }

      this.processing = false;
    }, (er) => {
      console.log('er:', er)
      this.processing = false;
    });
  }

  shouldDisableSubmit() {
    if (!this.formGroup.valid || this.processing === true) {
      return true;
    }
    return false;
  }

  getSubmitBtnLabel() {
    if (this.processing === true) {
      return 'please wait...';
    }
    return 'Login';
  }

}



