import { Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';
import { SubButton } from './sub-button';

@Component({
  selector: 'app-floating-action-buttons',
  templateUrl: './floating-action-buttons.component.html',
  styleUrls: ['./floating-action-buttons.component.scss']
})
export class FloatingActionButtonsComponent implements OnChanges {
  otherBtnsActive = false;
  @Input('active') active: boolean;
  @Input('size') size: string;
  @Input('mainBtnColor') mainBtnColor: string;
  @Input('disableSubmitBtn') disableSubmitBtn: boolean;
  @Input('subButtons') subButtons: SubButton[] = [];
  @Output('subButtonClicked') subButtonClicked: EventEmitter<any> = new EventEmitter();
  @Output('onMainBtnClicked') onMainBtnClicked: EventEmitter<any> = new EventEmitter();
  otherButtonsHideTimeout;
  constructor() { }

  ngOnChanges() {
    if ( this.active ) {
      clearTimeout(this.otherButtonsHideTimeout);
      this.otherBtnsActive = true;
    } else {
      this.otherButtonsHideTimeout = setTimeout(() => {
        this.otherBtnsActive = false;
      }, this.subButtons.length * 400);
    }
  }
    generateButtonStyles(button: SubButton, buttonIndex: number){
        const styles = {};
        const transitionDuration = 0.4;
        if (button.backgroundImage) {
            styles['background-image'] = 'url(' + button.backgroundImage + ')';
        }

        styles['transition'] = 'all ' + transitionDuration * (this.subButtons.length - buttonIndex) + 's ease-in-out';
        return styles;
    }
  onSubButtonClicked(buttonIndex) {
    this.subButtonClicked.emit({index: buttonIndex, name: this.subButtons[buttonIndex].name});
  }
  disableBtn(name) {
    this.subButtons.map((subButton: SubButton, i) => {
      if(subButton.name == name){
        subButton.disabled = true;
      }
      return subButton;
    });
  }
  enableBtn(name){
      this.subButtons.map((subButton: SubButton, i) => {
          if(subButton.name == name){
              subButton.disabled = false;
          }
          return subButton;
      });
  }
  changeButtonBackgroundImage(url, buttonName){
      this.subButtons.map((subButton: SubButton, i) => {
          if(subButton.name === buttonName){
              subButton.backgroundImage = url;
          }
          return subButton;
      });
  }

    changeSubButtonIcon(buttonName, icon){
        this.subButtons.map((subButton: SubButton, i) => {
            if(subButton.name === buttonName){
                subButton.icon = icon;
            }
            return subButton;
        });
    }

    mainBtnClicked(){
      this.onMainBtnClicked.emit();
    }
}



