import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { UserService } from '../../shared/services/user.service';
import { ContactsService } from '../../shared/services/contacts.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MessageService, SendMessageConfigs } from '../../shared/services/message.service';
import { GeneralService } from '../../shared/services/general.service';
import {GooglePickerService} from "../../shared/services/google-picker.service";

@Component({
  selector: 'app-add-contact',
  templateUrl: './add-contact.component.html',
  styleUrls: ['./add-contact.component.scss']
})
export class AddContactComponent implements OnInit {

    @Output()
    clickedB: EventEmitter<any> = new EventEmitter<any>();
    contactModel: any = {};
    processing = false;
    
    constructor(
        private ContactService: ContactsService,
        private generalService: GeneralService
    ) { }

    ngOnInit() {
    
    };
    
    createContacts() {
        this.processing = true;
        this.ContactService.addContacts(this.contactModel).subscribe((response: any) => {
            if (response.error== null) {
                this.contactModel = {};
                this.clickedB.emit(response);
                this.generalService.showNotification('Your contact is created successfully!', 'Close');

            }
            this.processing = false;
        });
    }

    getSubmitBtnLabel(){
        if (this.processing) {
            return 'Create + (Please wait...)';
        }
        return 'Create +';
    }

}



