import { Injectable } from '@angular/core';
import * as $ from 'jquery';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { Subject } from 'rxjs/Subject';
let gapi;
let google;

@Injectable()
export class GooglePickerService {
    onPicked: Subject<any> = new Subject();
    // The Browser API key obtained from the Google API Console.
    developerKey = 'AIzaSyCYNGGobFpUh1FusivMS93ti9UvwuukYQA';
    // The Client ID obtained from the Google API Console. Replace with your own Client ID.
    clientId = '70401002852-bt4dksgng2h1uvn2mv59tbjrfgj6um2m.apps.googleusercontent.com';
    // Scope to use to access user's photos.
    scope = ['https://www.googleapis.com/auth/drive', 'https://www.googleapis.com/auth/drive.readonly'];
    pickerApiLoaded = false;
    oauthToken;

    constructor() {
    this.initialize();
  }

  initialize() {
      this.loadScripts().subscribe(() => {
        gapi = window['gapi'];

        this.onApiLoad();
      });
  }

  loadScripts() {
    return new Observable((observer: Observer<any>) => {
        const el = document.createElement('script');

        el.src = 'https://apis.google.com/js/api.js';
        $('body')[0].appendChild(el);

        el.onload = () => {
            observer.next(' ');
            observer.complete();
        };
    });
  }

    onApiLoad() {
        gapi.load('auth');
        gapi.load('picker', {callback: () => {
                this.pickerApiLoaded = true;
                google = window['google'];
            }});
    }
    show() {
        if ( gapi.auth.getToken() === null) {
            gapi.auth.authorize({
                    client_id: this.clientId,
                    scope: this.scope,
                    immediate: false
                },
                (authResult) => {
                if (authResult && !authResult.error) {
                    this.oauthToken = authResult.access_token;
                    console.log('token', this.oauthToken);
                    this.createPicker();
                }
            });

        } else {
            this.createPicker();
        }
    }



    // Create and render a Picker object for picking user Photos.
    createPicker() {
        if (this.pickerApiLoaded && this.oauthToken) {

            const uploadView = new google.picker.DocsUploadView();
            uploadView.setMimeTypes('image/png,image/jpeg,image/jpg');

            const picker = new google.picker.PickerBuilder()
                .addView(uploadView).addView(google.picker.ViewId.DOCS_IMAGES).addView(google.picker.ViewId.RECENTLY_PICKED).
                setOAuthToken(this.oauthToken).
                setDeveloperKey(this.developerKey).
                setCallback(this.pickerCallback).
                build();
            picker.setVisible(true);
        }
    }

    pickerCallback  = (function(data) {
        if (data[google.picker.Response.ACTION] == google.picker.Action.PICKED) {
            const doc = data[google.picker.Response.DOCUMENTS][0];
            const fileId = doc[google.picker.Document.ID];
            const response = {fileId, googleAccessToken: this.oauthToken};
            this.onPicked.next(response);
        }
    }).bind(this);


}



