import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { ContactsService } from '../../../shared/services/contacts.service';
import { MatDialog, MatDialogRef } from '@angular/material';
import { GeneralService } from '../../../shared/services/general.service';
import { WebsocketService } from '../../../shared/services/websocket.service';
import { GooglePickerService } from '../../../shared/services/google-picker.service';
import { ThreadHeaderComponent } from './thread-header/thread-header.component';
import { ThreadBodyComponent } from './thread-body/thread-body.component';
import { DialogComponent } from '../../../shared/dialog/dialog.component';
import { MessageService } from '../../../shared/services/message.service';
import { Thread } from './thread';
import { ThreadContextMenuComponent } from './thread-context-menu/thread-context-menu.component';
import { UserService } from '../../../shared/services/user.service';
import { ApiService } from '../../../shared/services/api.service';

@Component({
    selector: 'app-thread',
    templateUrl: './thread.component.html',
    styleUrls: ['./thread.component.scss']
})

export class ThreadComponent implements OnInit {
    @Input('assigned_only') assigned_only;
    @Input('thread') thread: Thread; // Thread;
    @Input('contacts') contacts: any; // Thread;
    @Input('usage') usage;
    @Input('disableReplyFeature') disableReplyFeature: boolean;
    @ViewChild(ThreadHeaderComponent) ThreadHeaderComponent: ThreadHeaderComponent;
    @ViewChild(ThreadBodyComponent) ThreadBodyComponent;
    @ViewChild(ThreadContextMenuComponent) ThreadContexMenuComponent: ThreadContextMenuComponent;
    dialogRef: MatDialogRef<DialogComponent>;
    contextMenuItems = [
        {title: 'Delete message', icon: 'delete'}
    ];
    contact: any = {email: '', name: ''};
    displayTimer: any;

    constructor(private generalService: GeneralService,
                private googlePicker: GooglePickerService,
                private contactsService: ContactsService,
                private websocketService: WebsocketService,
                private dialog: MatDialog,
                private messageService: MessageService,
                private userService: UserService,
                private apiService: ApiService) {

    }

    ngOnInit() {
        this.thread.displayProperties = this.thread.displayProperties || {};

        // if usage is conversationPage then create contact object for ournumber if message is sent from ournumber
        if (this.usage === 'conversationPage' && this.thread.sent_from === this.thread.ournumber || (this.usage === 'contactPage' && this.isSentFromOurNumber())) {
            this.contact = {
                name: 'Me',
                number: this.isSentFromOurNumber() ? this.thread.sent_from : this.thread.theirnumber,
                email: ''
            };
        } else {
            // as message is sent from other person so use default method to prepare contact object
            this.contact = this.contactsService.getContact(this.contacts, this.thread.theirnumber);

            //If a new contact was created that matches the number of this message
            this.websocketService.contacts.create.subscribe((contact) => {
                if (contact.number === this.thread.theirnumber) {
                    this.contact = contact;
                }
            });
        }

        this.websocketService.messages.update.subscribe(message => {
            if (message.id === this.thread.id) {
                for (let prop in message) {
                    this.thread[prop] = message[prop];
                }
            }
        });

    }

    isSentFromOurNumber() {
        return this.userService.user.numbers.indexOf(this.thread.sent_from) >= 0
    }

    onMessageSendStart() {
        this.ThreadHeaderComponent.isProgressbarVisible = true;
    }

    onMessageSendComplete() {
        this.ThreadHeaderComponent.isProgressbarVisible = false;
    }

    shouldShowReplyForm() {
        return this.thread && this.thread.displayProperties && this.thread.displayProperties.shouldShowReplyForm;
    }

    hideAddContactForm() {
        this.ThreadHeaderComponent.hideAddContactForm();
    }

    getImageUrl() {
        if (!this.ThreadBodyComponent.fileId) {
            return this.thread.media_uri;
        } else if (this.ThreadBodyComponent.fileId) {
            return 'https://drive.google.com/uc?id=' + this.ThreadBodyComponent.fileId;
        } else {
            return false;
        }
    }

    hideReplyForm() {
        this.ThreadBodyComponent.hideReplyForm();
    }

    contextMenuStyles: any = {};

    getContextMenuStyles() {
        this.contextMenuStyles.position = 'fixed';
        this.contextMenuStyles.top = '100px';
        this.contextMenuStyles.left = '100px';
    }

    onContextMenuBtnClicked(menuItem) {
        if (menuItem.title === 'Delete message') {
            this.dialogRef = this.dialog.open(DialogComponent, {
                data: {
                    title: 'Delete message',
                    text: 'Are you sure you want to permanently delete this message?',
                    footerBtns: [
                        {label: 'Yes', color: 'primary', onCloseReturnData: {yes: 1}},
                        {label: 'No', color: 'default', onCloseReturnData: {yes: 0}},
                    ]
                }
            });
        }

        this.registerDialogEvent();
    }

    registerDialogEvent() {
        this.dialogRef.afterClosed().subscribe((response: any) => {
            if (response.yes === 1) {
                this.hideContextMenu();
                this.ThreadHeaderComponent.isProgressbarVisible = true;
                this.messageService.delete(this.thread.id).subscribe((r: any) => {
                    if (r.success) {
                        this.generalService.showNotification('Message deleted successfully.', 'Okay');
                    }
                }, () => {
                    this.ThreadHeaderComponent.isProgressbarVisible = false;
                }, () => {
                });

            }
        });
    }

    showContextMenu(event) {
        this.ThreadContexMenuComponent.showContextMenu(event);
    }

    hideContextMenu() {
        this.ThreadContexMenuComponent.hideContextMenu();
    }

    stopGlowing() {
        this.ThreadHeaderComponent.isGlowing = false;
    }

    onAssignUserClicked(user) {

        this.ThreadContexMenuComponent.hideContextMenu();

        this.apiService.call('/apis/assignments/create', {
            ournumber: this.thread.ournumber,
            theirnumber: this.thread.theirnumber,
            user: user.id

        }).subscribe((response)=>{
            if(response.error==null){
                this.generalService.showNotification('Conversation is assigned successfully.', 'Okay');
            }
        });
    }

}



