import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ContactsService } from '../../shared/services/contacts.service';
import { Contact } from '../contact';
import { FormGroup } from '@angular/forms';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/filter';
import { GeneralService } from '../../shared/services/general.service';
import { Observable } from 'rxjs/Observable';
import { TagsService } from '../../shared/services/tags.service';
import { ContextMenuComponent } from '../../shared/context-menu/context-menu.component';
import { DialogComponent } from '../../shared/dialog/dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material';
import { ApiService } from '../../shared/services/api.service';
import { Message } from '../../conversation/message/message';
import { WebsocketService } from '../../shared/services/websocket.service';

@Component({
    selector: 'app-contact',
    templateUrl: './contact.component.html',
    styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

    contact: Contact = {
        id: null,
        account: null,
        email: '',
        name: '',
        number: '',
        user: null,
        date_created: '',
        date_updated: '',
        tags: []
    };
    contextMenuItems = [
        { title: 'Delete contact', icon: 'delete' }
    ];
    formGroup: FormGroup;
    messages: Message[] = [];
    contacts: Contact[] = [];
    savingTags = false;
    savingName = false;
    savingEmail = false;
    savingNumber = false;
    loadingContact = false;
    isProgressbarVisible = false;

    dialogRef: MatDialogRef<DialogComponent>;
    @ViewChild(ContextMenuComponent) ContexMenuComponent: ContextMenuComponent;

    constructor(private route: ActivatedRoute,
        private contactsService: ContactsService,
        private generalService: GeneralService,
        private dialog: MatDialog,
        private tagsService: TagsService,
        private router: Router,
        private webSocketService: WebsocketService,
        private apiService: ApiService) {
    }

    ngOnInit() {
        const id = this.route.snapshot.params['id'];
        this.loadContact(id);
        this.webSocketService.contacts.update.subscribe((data) => {
            if(data.contact && data.contact.id==this.contact.id){
                this.contact = data.contact;
            }

        });
        this.webSocketService.contacts.delete.subscribe((data) => {

            if (data.id == this.contact.id) {
                this.router.navigateByUrl('/app/contacts');
            }

        });

    }

    loadContact(id) {

        this.loadingContact = true;

        this.contactsService.get(id, { includeTags: true }).subscribe((response: any) => {
            if (response.success == true) {
                this.contact = response.data;
                this.loadingContact = false;
                this.loadMessages();
            }
        }, () => {
            this.loadingContact = false;
        });
    }

    stringify(data) {
        return JSON.stringify(data);
    }

    saveTags() {

        this.savingTags = true;
        // remove the tags from database which are not available in tags array
        const blnDeleteMissingTags = true;
        this.contactsService.addTags(this.contact.tags, this.contact.id, blnDeleteMissingTags).subscribe((response: any) => {
            if (response.success) {

                this.generalService.showNotification('Tags are saved successfully.', 'Okay');

            }

            this.savingTags = false;

        }, () => {
            this.savingTags = false;
        });
    }

    save(field) {

        this.changeSaveProgress(field, true);

        const data = {};

        data[field] = this.contact[field];
        data['id'] = this.contact.id;

        this.contactsService.update(data).subscribe((response: any) => {

            if (response.success) {

                this.generalService.showNotification(field.charAt(0).toUpperCase() + field.slice(1) + ' is updated successfully.', 'Okay');

            }

            this.changeSaveProgress(field, false);

        }, () => {
            this.changeSaveProgress(field, false);
        });
    }

    changeSaveProgress(field, state) {
        switch (field) {
            case 'name':
                this.savingName = state;
                break;
            case 'email':
                this.savingEmail = state;
                break;
            case 'number':
                this.savingNumber = state;
                break;
        }
    }

    requestAutocompleteItems = (text: string): Observable<Response> => {

        const options = { contains: text };
        return this.tagsService.getTags(options).map((resp) => {
            return resp.results;
        });
    }

    showContextMenu(event) {
        this.ContexMenuComponent.showContextMenu(event);
    }

    onContextMenuBtnClicked(menuItem) {
        if (menuItem.title === 'Delete contact') {
            this.dialogRef = this.dialog.open(DialogComponent, {
                data: {
                    title: 'Delete contact',
                    text: 'Are you sure you want to permanently delete this contact?',
                    footerBtns: [
                        { label: 'Yes', color: 'primary', onCloseReturnData: { yes: 1 } },
                        { label: 'No', color: 'default', onCloseReturnData: { yes: 0 } },
                    ]
                }
            });
        }

        setTimeout(() => {
            this.registerDialogEvent();
        }, 100);

    }

    registerDialogEvent() {
        this.dialogRef.afterClosed().subscribe((response: any) => {
            if (response.yes === 1) {

                this.delete();

            }
        });
    }

    delete() {
        this.isProgressbarVisible = true;
        this.contactsService.delete(this.contact.id).subscribe((r: any) => {
            if (r.success) {
                this.generalService.showNotification('Contact    deleted successfully.', 'Okay');

                this.router.navigateByUrl('/app/contacts');

            }
        }, () => {
            this.isProgressbarVisible = false;
        }, () => {
        });
    }

    loadMessages() {
        this.apiService.call('/api/messages/getMessages', { TheirNumber: this.contact.number }).subscribe((response: any) => {

            if (response.contacts) {
                this.contacts = response.contacts;
            }


            if (response.messages) {
                this.messages = response.messages
            }
        });
    }

}



