import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MessageService } from '../shared/services/message.service';
import { ApiService } from '../shared/services/api.service';
import { WebsocketService } from '../shared/services/websocket.service'
import { Thread } from './thread.module/thread/thread';
import { UserService } from '../shared/services/user.service';
import { NumberThreadsContextMenuComponent } from './number-threads-context-menu/number-threads-context-menu.component';
import { ActivatedRoute, Router } from '@angular/router';
import { Contact } from '../contacts/contact';

@Component({
    selector: 'app-number-threads',
    templateUrl: './number-threads.component.html',
    styleUrls: ['./number-threads.component.scss']
})
export class NumberThreadsComponent implements OnInit, OnDestroy {
    @ViewChild(NumberThreadsContextMenuComponent) NumberThreadsContextMenuComponent;
    @Input('numberObj') numberObj;
    @Input('usage') usage;
    @Input('page') pageNumber = 1;
    processing = false;
    assigned_only = false;
    threads: Thread[] = [];
    contacts = []
    threadLimit: number = 9;
    perPageThreads: number = 6;
    routerSubscription;

    constructor(
        private messageService: MessageService,
        private apiService: ApiService,
        private webSocketService: WebsocketService,
        private userService: UserService,
        private route: ActivatedRoute,
        private router: Router
    ) { }

    ngOnDestroy(){

            this.routerSubscription.unsubscribe();
    }

    ngOnInit() {



        this.routerSubscription = this.route.params.subscribe((event) => {

            setTimeout(()=>{
                this.load(this.pageNumber );
            }, 100);
        });


        this.webSocketService.messages.create.subscribe(msg => {
            // adding isGlowing: true property in msg to glow new message

            // glow only if message received
            if (this.userService.user.numbers.indexOf(msg.sent_from) < 0) {
                msg.isGlowing = true;
            }
            console.log('Message in number-thread:', msg)


            if (msg.ournumber != this.numberObj.number) return //The message isn't for this number
            let m = this.messageService
            if (m.conversationExistsInMessageArray(msg, this.threads)) { //Already have a message for this conversation
                var oldMessage = m.returnMatchingConversation(msg, this.threads)
                if (m.messageIsNewerThanMessage(msg, oldMessage)) {
                    m.replaceMessageInArray(msg, oldMessage, this.threads) //Only want 1 message per conversation in this view
                } else {
                    console.log('OLDER MESSAGE CAME IN VIA THE WEBSOCKET')
                    //Do nothing we don't want older messages
                }
            } else { //This is brand new message and conversation
                m.addMessageToArrayWithLimit(msg, this.threads, this.threadLimit, true) //Add the new message
            }
        })



        this.webSocketService.contacts.create.subscribe(contact => {
            console.log('RECEIVED A CONTACT VIA THE WEBSOCKET')
            this.contacts.push(contact)
            console.log(contact)
        });

        this.webSocketService.messages.delete.subscribe((data: any) => {
            console.log('REMOVED A MESSAGE VIA THE WEBSOCKET')
            this.load();
        })


        this.webSocketService.refresh.subscribe(ref => {

            this.load() //The websocket tries to maintain a connection and emmits the refresh event if it lost connection
        })
    }

    onAssignedOnlyToggle($event){
        this.assigned_only = $event.checked;
        this.load();
    }

    load(pageNumber=1) {
        if (pageNumber < 1) {
            return false;
        }

        this.processing = true;
        const options: any = {ournumber: this.numberObj.number, pageNumber};
        if(this.assigned_only){
            options.assigned_only = true;
        }

        if(this.usage=='numberPage'){
            options.limit = this.perPageThreads;
        }


        this.apiService.call('/components/number-thread', options).subscribe(resp => {
            resp && resp.messages ? this.threads = resp.messages : setTimeout(() => this.load(), 10000) //Try again in 10 seconds
            resp && resp.contacts ? this.contacts = resp.contacts : console.log('Didnt get what I wanted from number-threads call')

            this.processing = false;

        }, ()=>{
            this.processing = false;
        });
    }

    updatePageNumberInUrl(){
        this.router.navigateByUrl('/app/number/'+this.numberObj.number+'/page/'+this.pageNumber);
    }

    showSendMessageWindow() {
        this.messageService.showSendMessageWindow({ useAcountNumberObj: this.numberObj });
    }

    showContextMenu($event){
        this.NumberThreadsContextMenuComponent.showContextMenu($event);
    }

    loadNextPage(){
        ++this.pageNumber;
        this.updatePageNumberInUrl();
    }

    loadPrevPage(){
        if(this.pageNumber>1){
            --this.pageNumber;
            this.updatePageNumberInUrl();
        }

    }

}



