import { Component, Inject, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { TagsService } from '../services/tags.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { AddFilterDialogComponent } from '../../needles/needles-persons/add-filter-dialog/add-filter-dialog.component';

@Component({
  selector: 'app-create-tags-dialog',
  templateUrl: './create-tags-dialog.component.html',
  styleUrls: ['./create-tags-dialog.component.scss']
})
export class CreateTagsDialogComponent implements OnInit {

  tags = [];
  constructor(
      private tagsService: TagsService,
      public dialogRef: MatDialogRef<AddFilterDialogComponent>,
      @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit() {
  }

    requestAutocompleteItems = (text: string): Observable<Response> => {

        const options = { contains: text };
        return this.tagsService.getTags(options).map((resp) => {
            return resp.results;
        });
    }

    add(){
      this.dialogRef.close({tags: this.tags});
    }

}



