import { Injectable } from '@angular/core';
import {Subject} from 'rxjs/Subject';

@Injectable()
export class ImageViewerService {
  onShow: Subject<any> = new Subject<any>();
  constructor() { }
  show(mediaUrl) {
    this.onShow.next(mediaUrl);
  }

}



