import { Component, OnInit } from '@angular/core';
import { ApiService } from '../shared/services/api.service';
import { MatDialog, MatDialogRef } from '@angular/material';
import { DialogComponent } from '../shared/dialog/dialog.component';
import { GeneralService } from '../shared/services/general.service';

@Component({
    selector: 'app-notifications',
    templateUrl: './notifications.component.html',
    styleUrls: ['./notifications.component.scss']
})
export class NotificationsComponent implements OnInit {

    notifications: any = [];
    processing = false;

    constructor(private apiService: ApiService, private generalService: GeneralService) {
    }

    ngOnInit() {

        this.getNotifications();

    }

    getNotifications() {

        this.processing = true;

        this.apiService.call('/api/messages/getNotifications').subscribe((response: any) => {
            this.processing = false;
            if (response.success) {
                this.notifications = response.data.notifications;
            }
        }, ()=>{
            this.processing = false;
        });

    }

    onNotificationDelete(notification){

        const notifcationIndex = this.notifications.indexOf(notification);
        notification = this.notifications[notifcationIndex];

        notification.zeroOpacity = true;

        setTimeout(()=>{
            notification.zeroWidth = true;
            setTimeout(()=>{

                this.notifications.splice(notifcationIndex, 1);

            }, 400);
        }, 400);


    }



}



