import { Component, EventEmitter, Input, OnChanges, OnInit, Output, ViewChild } from '@angular/core';
import { SubButton } from '../../floating-action-buttons/sub-button';
import { GooglePickerService } from '../../services/google-picker.service';
import { DateTimePickerModalComponent } from '../../date-time-picker-modal/date-time-picker-modal.component';
import { MatDialog, MatDialogRef } from '@angular/material';
import { FloatingActionButtonsComponent } from '../../floating-action-buttons/floating-action-buttons.component';

@Component({
  selector: 'app-message-form-floating-buttons',
  templateUrl: './message-form-floating-buttons.component.html',
  styleUrls: ['./message-form-floating-buttons.component.scss']
})
export class MessageFormFloatingButtonsComponent implements OnChanges, OnInit {
  dateTimePickerRef: MatDialogRef<DateTimePickerModalComponent>;
  googlePickerSubscription;
  @Input('mainBtnColor') mainBtnColor;
  @Input('usedIn') usedIn;
  @Input('size') size;
  @Input('active') active;
  @Input('disableSubmitBtn') disableSubmitBtn;
  @Input('subButtons') subButtons: SubButton[];
  @Output('photoSelectionStart') photoSelectionStart: EventEmitter <any> = new EventEmitter();
  @Output('photoSelectionEnd') photoSelectionEnd: EventEmitter <any> = new EventEmitter();
  @Output('scheduleSelectionStart') scheduleSelectionStart: EventEmitter <any> = new EventEmitter();
  @Output('scheduleSelectionEnd') scheduleSelectionEnd: EventEmitter <any> = new EventEmitter();
  @Output('discard') discard: EventEmitter <any> = new EventEmitter();
  @Output('mainBtnClicked') mainBtnClicked: EventEmitter <any> = new EventEmitter();
  @Output('submit') submit: EventEmitter <any> = new EventEmitter();
  @ViewChild(FloatingActionButtonsComponent) FloatingActionButtonsComponent;
  defaultSubBtns = [
      {
          color: 'warn',
          tooltipText: 'Send photo!',
          tooltipDirection: 'left',
          icon: 'photo_camera',
          name: 'photo',
      },
      {
          color: 'accent',
          tooltipText: 'Schedule',
          tooltipDirection: 'left',
          icon: 'date_range',
          name: 'schedule',
      },
      {
          color: 'default',
          tooltipText: 'Discard',
          tooltipDirection: 'left',
          icon: 'delete',
          name: 'delete',
      },
      {
          color: 'primary',
          tooltipText: 'Send Message',
          tooltipDirection: 'left',
          icon: 'send',
          name: 'submit',
          disabled: true,
      }];
  constructor(
      private googlePicker: GooglePickerService,
      private dialog: MatDialog
  ) {

  }

  ngOnInit() {
      this.setupFloatingButtons();

  }
  ngOnChanges(changes) {

    setTimeout(() => {
        if ( this.subButtons.length > 0  ) {
            if ( this.disableSubmitBtn === true) {
                this.FloatingActionButtonsComponent.disableBtn('submit');
            } else {
                this.FloatingActionButtonsComponent.enableBtn('submit');
            }
        }

    }, 100);
  }

    selectPhoto() {
        this.photoSelectionStart.emit();
        this.googlePickerSubscription = this.googlePicker.onPicked.subscribe((response: any) => {
            if (this.usedIn !== 'replyForm') {
                this.FloatingActionButtonsComponent.changeButtonBackgroundImage('https://drive.google.com/uc?id=' + response.fileId, 'photo');
            }
            this.photoSelectionEnd.emit(response);
            this.googlePickerSubscription.unsubscribe();
        });
        this.googlePicker.show();
    }
    onClickFloatingSubBtn(data) {
        const btnIndex = data.index;
        const btnName = data.name;
        switch (btnName) {
            case 'photo':
                this.selectPhoto();
                break;

            case 'delete':
                this.discard.emit();
                break;

            case 'schedule':
                this.showDateTimePicker();
                break;

            case 'submit':
                this.submit.emit();
                break;
        }
    }

    showDateTimePicker() {
        this.scheduleSelectionStart.emit();
        this.dateTimePickerRef = this.dialog.open(DateTimePickerModalComponent, {
            data: {
                title: 'Schedule',

            }
        });

        this.registerDateTimePickerEvents();
    }

    registerDateTimePickerEvents() {
        this.dateTimePickerRef.afterClosed().subscribe((result: any) => {
            let scheduledDateTime = '';
            if (result.status === 1) {
                scheduledDateTime = new Date(result.date + ' ' + result.time).toISOString();
                this.FloatingActionButtonsComponent.changeSubButtonIcon('submit', 'save');
            }
            this.scheduleSelectionEnd.emit(scheduledDateTime);
        });
    }

    setupFloatingButtons() {
    // making clone of default buttons
        const subButtons = JSON.parse(JSON.stringify(this.defaultSubBtns));
        subButtons.map((button: SubButton) => {
          if (button.name === 'submit') {
            button.disabled = this.disableSubmitBtn;
          }
        });
        this.subButtons = subButtons;
    }

    reset() {
      // making clone of default buttons
      const subButtons =  JSON.parse(JSON.stringify(this.defaultSubBtns));
      this.subButtons = subButtons;
    }

    resetSubmitIcon() {
        // get default icon for submit btn
        const icon = this.defaultSubBtns.filter((btn: SubButton) => btn.name === 'submit')[0]['icon'];
        this.FloatingActionButtonsComponent.changeSubButtonIcon('submit', icon);
    }

    onMainBtnClicked() {
      this.mainBtnClicked.emit();
    }

}



