import { Injectable, EventEmitter } from '@angular/core';
import { Subject } from "rxjs/Subject";
import { Observable } from "rxjs/Observable";
import { Observer } from "rxjs/Observer";
import { TimerService } from './timer.service';
import 'rxjs/add/operator/map'
import { environment } from "../../../environments/environment";

@Injectable()
export class WebsocketService {
  constructor(private timerService: TimerService) {


    this.timerService.tenSecondTimer.subscribe((date) => {
      this.checkStatusAndRestart(date)
    })
  }



  private WEBSOCKET_URL: string = environment.WEBSOCKET_URL || 'ws://clientconnectdashboardwebsocket1.azurewebsites.net'
  //private WEBSOCKET_URL: string = 'ws://localhost:3333'
  private subject: Subject<MessageEvent>;
  private pongDate: Date

  public messages = {
    create: new EventEmitter<any>(),
    update: new EventEmitter<any>(),
    delete: new EventEmitter<any>()
  }

  public contacts = {
    create: new EventEmitter<any>(),
    update: new EventEmitter<any>(),
    delete: new EventEmitter<any>()
  }

  public tags = {
    create: new EventEmitter<any>(),
    update: new EventEmitter<any>(),
    delete: new EventEmitter<any>()
  }


  public refresh = new EventEmitter<any>();
  public startUpData: any

  sender

  private maxDownTime = 1000 * 30 //30 seconds


  public start(data?) {
    if (data) this.startUpData = data
    this.subject = undefined
    this.pongDate = new Date()
    this.sender = this.connect(this.WEBSOCKET_URL).map((response: MessageEvent) => {
      let data = JSON.parse(response.data);
      return data
    });
    this.sender.next(this.startUpData)
    return this.sender
  }



  private checkStatusAndRestart(date) {
    if (!this.pongDate) this.pongDate = new Date()
    this.sender.next({ type: 'ping' })
    if (new Date().valueOf() - this.pongDate.valueOf() > this.maxDownTime) {
      this.refresh.emit(new Date())
      this.start()
    }
  }



  private connect(url): Subject<MessageEvent> {
    if (!this.subject) {
      this.subject = this.create(url);
      console.log("Successfully connected: " + url);
    }


    this.subject.subscribe((response: MessageEvent) => {
      let data = JSON.parse(response.data);
      switch (data.type) {
        case 'message':
          console.log('in message switch')
          switch (data.method) {
            case 'create':
              this.messages.create.emit(data.data)
              break
            case 'update':
              this.messages.update.emit(data.data)
              break
            case 'delete':
              this.messages.delete.emit(data.data)
              break
            default:
              console.log('Websocket emitted message with incorrect method. See data: ', data)
              break;
          }
          break;


        case 'contact':
          switch (data.method) {
            case 'create':
              this.contacts.create.emit(data.data)
              break
            case 'update':
              this.contacts.update.emit(data.data)
              break
            case 'delete':
              this.contacts.delete.emit(data.data)
              break
            default:
              console.log('Websocket emitted contact with incorrect method. See data: ', data)
              break;
          }
          break;
        case 'pong':
          this.pongDate = new Date()
          break
        default:
          console.log('Websocket Emitted something unexpected:', data)


      }
    });


    return this.subject;
  }



  private create(url): Subject<MessageEvent> {
    let ws = new WebSocket(url);

    let observable = Observable.create(
      (obs: Observer<MessageEvent>) => {
        ws.onmessage = obs.next.bind(obs);
        ws.onerror = obs.error.bind(obs);
        ws.onclose = obs.complete.bind(obs);
        return ws.close.bind(ws);
      })
    let observer = {
      next: (data: Object) => {
        if (ws.readyState === WebSocket.OPEN) {
          ws.send(JSON.stringify(data));
        } else {
          ws.onopen = function () {
            console.log('onopen')
            ws.send(JSON.stringify(data));
          }
        }
      }
    }
    return Subject.create(observer, observable);
  }

}



