import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {SubButton} from "../sub-button";

@Component({
  selector: 'app-sub-button',
  templateUrl: './sub-button.component.html',
  styleUrls: ['./sub-button.component.scss']
})
export class SubButtonComponent implements OnInit {
  @Input('size') size: string;
  @Input('totalButtons') totalButtons: number;
  @Input('buttonIndex') buttonIndex: number;
  @Input('button') button: SubButton;
  @Output('subButtonClicked') subButtonClicked: EventEmitter<any> = new EventEmitter();
  constructor() { }

  ngOnInit() {
  }

    generateButtonStyles() {
        const styles = {};
        const transitionDuration = 0.4;
        if (this.button.backgroundImage) {
            styles['background-image'] = 'url(' + this.button.backgroundImage + ')';
        }
        styles['transition'] = 'all ' + transitionDuration * (this.totalButtons - this.buttonIndex) + 's ease-in-out';
        return styles;
    }

    onSubButtonClicked(){
      this.subButtonClicked.emit();
    }

}



