import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import {MatDialogRef} from '@angular/material';
import {NumberlistCardComponent} from './../numberlist-card/numberlist-card.component';
import { NumberService } from './../../shared/services/number.service';
import {MAT_DIALOG_DATA} from '@angular/material';

@Component({
  
  templateUrl: './delete-number-warning-modal.component.html',
  styleUrls: ['./delete-number-warning-modal.component.scss']
})
export class DeleteNumberWarningModalComponent implements OnInit {

  
  public deleteTwillioAreaNumbersLoaderVisualization: Boolean = false;
  constructor(
    private NumberService: NumberService,
    public dialogRef: MatDialogRef<DeleteNumberWarningModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit() {
  }

  closeDialog() {
    this.dialogRef.close(this.data);
  };
  
  removeNumber() {
      this.deleteTwillioAreaNumbersLoaderVisualization = true;
      this.NumberService.removeNumber(this.data).subscribe((response: any ) => {
        
          console.log(response);
          this.deleteTwillioAreaNumbersLoaderVisualization = false;
          this.dialogRef.close('new');
      }, (response: any) => {
        let error;
        this.deleteTwillioAreaNumbersLoaderVisualization = false;
        if (response.error.error.message){
            error = response.error.error.message;
        } else {
            error = response.error.error;
        }
      });

    
  }

}



