import { Injectable } from '@angular/core';
import { ApiService } from './api.service';

@Injectable()
export class NumberService {

    constructor(
        private apiService: ApiService
    ) { }

    removeNumber(data) {
        return this.apiService.call('/api/removeNumber', data);
    }

    addAddress(data) {
        return this.apiService.call('/api/addressCreate', data);
    }

    getNumbers(searchKeyword: string) {
        return this.apiService.call('/api/getNumbers', { contains: searchKeyword });
    }

    getAreaNumbers(code: string) {
        return this.apiService.call('/api/getAreaNumbers', { areacode: code });
    }

    addNumbers(number: string) {
        return this.apiService.call('/api/buyNumber', { number });
    }

    addHostedNumber(data) {
        return this.apiService.call('/api/numbers/createHostedNumber', data);
    }

    callVerification(data) {
        data.extension = data.extension || '';
        return this.apiService.call('/api/numbers/verification/phoneCall', data);
    };

    createLoaDocument(sid) {
        return this.apiService.call('/api/numbers/verification/createLOA', { sid });
    }

    submitVerificationCode(code, sid) {
        return this.apiService.call('/api/numbers/submitVerificationCode', { code, sid });
    }
}


