import { Component, Input, OnInit } from '@angular/core';
import { MessageService } from '../../shared/services/message.service'
import {ImageViewerService} from "../../shared/image-viewer/image-viewer.service";
import { ContactsService } from '../../shared/services/contacts.service';

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.scss']
})
export class MessageComponent implements OnInit {

  @Input('accountNumber') accountNumber;
  @Input('message') message: any //Message;
  @Input('contacts') contacts: any;

  constructor(
      private messageService: MessageService,
      private imageViewer: ImageViewerService,
      private contactsService: ContactsService
  ) { }

  ngOnInit() {
  }

  showTime = this.messageService.showTime;

    showImageViewer(url) {
        this.imageViewer.show(url);
    }


}



