import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { DialogComponent } from '../../shared/dialog/dialog.component';
import { MatDialog } from '@angular/material';
import { GeneralService } from '../../shared/services/general.service';
import { ApiService } from '../../shared/services/api.service';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss']
})
export class NotificationComponent implements OnInit {
    dialogRef;
    processing = false;
  @Input('notification') notification;
  @Output('notificationDeleted') notificationDeleted = new EventEmitter();

  constructor(
      private dialog: MatDialog,
      private generalService: GeneralService,
      private apiService: ApiService
  ) { }

  ngOnInit() {
  }

    deleteNotification() {

        this.dialogRef = this.dialog.open(DialogComponent, {
            data: {
                title: 'Unsubscribe',
                text: 'Are you sure you want to unsubscribe?',
                footerBtns: [
                    {label: 'Yes', color: 'primary', onCloseReturnData: {yes: 1}},
                    {label: 'No', color: 'default', onCloseReturnData: {yes: 0}},
                ]
            }
        });

        this.dialogRef.afterClosed().subscribe((response: any) => {
            if (response.yes) {
                this.processing = true;
                this.apiService.call('/api/messages/deleteNotification', {id: this.notification.id}).subscribe((r: any) => {
                    this.processing = false;
                    if (r.success) {
                        this.generalService.showNotification('You have been unsubscribed successfully.', 'Okay');

                        this.notificationDeleted.emit(this.notification);

                    }
                }, ()=>{
                    this.processing = false;
                });

            }
        });


    }

}



