import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import {MatDialogRef} from '@angular/material';
import {NumberlistCardComponent} from './../numberlist-card/numberlist-card.component';
import { NumberService } from './../../shared/services/number.service';
import {MAT_DIALOG_DATA} from '@angular/material';



@Component({
  selector: 'app-verification-call-warning-modal',
  templateUrl: './verification-call-warning-modal.component.html',
  styleUrls: ['./verification-call-warning-modal.component.scss']
})
export class VerificationCallWarningModalComponent implements OnInit {
  public deleteHostedNumbers: Boolean = false;
  constructor(
    private NumberService: NumberService,
    public dialogRef: MatDialogRef<VerificationCallWarningModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { 
    
  }

  ngOnInit() {
  }


  closeDialog() {
    this.dialogRef.close(this.data);
  };
  
  verificationCall() {
      this.NumberService.callVerification(this.data).subscribe((response: any ) => {
          console.log(response);
          this.deleteHostedNumbers = false;
          this.dialogRef.close(response.result);
      }, (response: any) => {
        let error;
        this.deleteHostedNumbers = false;
        if (response.error.error.message){
            error = response.error.error.message;
        } else {
            error = response.error.error;
        }
      });
  }

}



