import { Component, OnInit, Input, ViewChild, Output, EventEmitter } from '@angular/core';
import { MessageService, SendMessageConfigs } from '../../../../shared/services/message.service';
import { Thread } from '../thread';
import { MessageFormFloatingButtonsComponent } from '../../../../shared/message-form-floating-buttons/message-form-floating-buttons/message-form-floating-buttons.component';
import { GeneralService } from '../../../../shared/services/general.service';
import { ThreadComposeComponent } from '../compose/thread-compose.component';

@Component({
  selector: 'app-thread-body',
  templateUrl: './thread-body.component.html',
  styleUrls: ['./thread-body.component.scss']
})
export class ThreadBodyComponent implements OnInit {

  @ViewChild(MessageFormFloatingButtonsComponent) MessageFormFloatingButtonsComponent;
  @Input('thread') thread: Thread;
  @Input('usage') usage;
  @Input('disableReplyFeature') disableReplyFeature: boolean;
  @Output('messageSendStart') onMessageSendStart = new EventEmitter();
  @Output('messageSendComplete') onMessageSendComplete = new EventEmitter();
  selectingSchedule = false;
  scheduledTime;
  fileId;
  googleAccessToken;
  formOtherBtnsZIndex = -1111;
  selectingImage: boolean;

  constructor(
    private messageService: MessageService,
    private generalService: GeneralService
  ) { }

  ngOnInit() {
  }

  refresh(action = 'refresh') {
    this.hideReplyForm(action);
    this.thread.displayProperties.replyMsg = '';
    this.thread.displayProperties.processing = false;
    this.fileId = '';
    this.scheduledTime = '';
    this.MessageFormFloatingButtonsComponent.reset();
  }
  onMsgCancel() {
    this.hideReplyForm();
  }

  send() {
    this.onMessageSendStart.emit();
    this.thread.displayProperties.processing = true;
    const configs: SendMessageConfigs = {
      body: this.thread.displayProperties.replyMsg,
      to: this.thread.theirnumber,
      from: this.thread.ournumber
    };
    if (this.fileId) {
      configs.fileId = this.fileId;
    }
    if (this.scheduledTime) {
      configs.scheduled_time = this.scheduledTime;
    }
    if (this.googleAccessToken) {
      configs.googleAccessToken = this.googleAccessToken;
    }

    this.messageService.sendMessage(configs).subscribe((response: any) => {
      if (response.sent === true) {
        this.refresh('msgSent');
      }
      this.onMessageSendComplete.emit();
    }, (error) => {
      this.thread.displayProperties.processing = false;
      this.onMessageSendComplete.emit();
    });
  }

  hideReplyForm(action = '') {

    if ((this.selectingSchedule || this.selectingImage) && action !== 'msgSent') {
      return false;
    }
    this.thread.displayProperties.shouldShowReplyForm = false;
    setTimeout(() => {
      this.formOtherBtnsZIndex = -111111;
    }, 500);
  }

  onPhotoSelectionStart() {
    this.selectingImage = true;
  }
  onPhotoSelectionEnd(response) {
    this.fileId = response.fileId;
    this.googleAccessToken = response.googleAccessToken;
    this.selectingImage = false;
  }

  removeTimer() {
    this.scheduledTime = '';
    this.MessageFormFloatingButtonsComponent.resetSubmitIcon();
  }

  showReplyForm() {
    this.thread.displayProperties.shouldShowReplyForm = true;
    this.formOtherBtnsZIndex = 111;
  }

  open() {
    this.thread.displayCompose = true;
  }

  onScheduleSelectionStart() {
    this.selectingSchedule = true;
  }
  onScheduleSelectionEnd(scheduleTime) {
    this.scheduledTime = scheduleTime;
    this.selectingSchedule = false;
  }

  onSubmit() {
    this.send();
  }

  processing() {
    return this.thread && this.thread.displayProperties && this.thread.displayProperties.processing;
  }

}



