import { Component, OnInit } from '@angular/core';
import { APPCONFIG } from '../../config';
import { UserService } from '../../shared/services/user.service';
import { MessageService } from '../../shared/services/message.service';
import { WebsocketService } from '../../shared/services/websocket.service';

import { User } from '../../shared/interfaces/user';
import { Router } from '@angular/router';

@Component({
  selector: 'my-app-header',
  styles: [],
  templateUrl: './header.component.html'
})

export class AppHeaderComponent implements OnInit {
  public AppConfig: any;
  user: User;
  //notifications

  constructor(
    private userService: UserService,
    private messageService: MessageService,
    private webSocketService: WebsocketService,
    private router: Router,
  ) {
    this.user = this.userService.user
    this.webSocketService.messages.create.subscribe(msg => {
      if (this.userService.user.numbers.indexOf(msg.sent_to) != -1) {
        let m = this.messageService
        let nots = this.notifications
        if (m.conversationExistsInMessageArray(msg, nots)) {
          var match = m.returnMatchingConversation(msg, nots)
          if (m.messageIsNewerThanMessage(msg, match)) {
            this.remove(match) //Take out the old message
            this.addNotification(msg) //Add in the new message
          } else {
            //do nothing - we don't want to notify of older messages
          }
        } else {
          //This is a new conversation - add the message
          this.addNotification(msg)
        }

      }
    })
  }

  notifications = []

  addNotification(msg) {
    this.notifications.push(msg)
  }

  remove(msg) {
    var indx = this.notifications.indexOf(msg)
    if (indx > -1) this.notifications.splice(indx, 1)
  }

  ngOnInit() {
    this.AppConfig = APPCONFIG;
  }

  showTime = this.messageService.showTime

  logout() {
    this.userService.logout();
  }

    openPage(page){
      this.router.navigateByUrl(page);

    }
}



