import { Injectable } from '@angular/core';
import { UserService } from './user.service';
import 'rxjs/add/operator/share';
import { ApiService } from './api.service';

@Injectable()
export class ContactsService {

    constructor(
        private userService: UserService,
        private apiService: ApiService,
    ) { }

    addContacts(contactModel) {

        let body: any = {
            name: contactModel.name,
            number: contactModel.number,
        };
        if(contactModel.email){
            body.email =  contactModel.email;
        }

        return this.apiService.call('/api/createContacts', body)
    }


    addTag(tagModel: any) {
        let body: any = {
            contactId: tagModel.contactId,
            name: tagModel.name
        };

        return this.apiService.call('/api/createTag', body)
    }

    addTags(tags, contactId, blnDeleteMissingTags= false) {

        tags = JSON.stringify(tags);

        const body: any = {
            contactId,
            tags,
            blnDeleteMissingTags
        };

        return this.apiService.call('/api/createTags', body);
    }

    removeTags(tagName, contactId) {
        let body: any = {};
        body.contactId = contactId;
        body.tagName = tagName;
        return this.apiService.call('/api/removeTags', body)
    }

    getContact(contacts, number) {
        for (let contact of contacts) {
            if (contact.number == number) return contact
        }
        return {
            number: number,
            name: '',
            //name: number.length == 12 ? '(' + number.slice(2, 5) + ')' + number.slice(5, 8) + '-' + number.slice(-4) : number,
            initials: number.slice(-4).split('').join(' ')
        }
    }

    getContacts(options: any={contains:''}){
        const body = {};
        body['contains'] = options.contains;
        return this.apiService.call('/components/contacts', body);
    }

    get(id: number, options?: any){
        const body: any = {};
        if(options){
            if(options.includeTags){
                body.includeTags = true;
            }
        }
        body['id'] = id;
        return this.apiService.call('/api/contacts/get', body);
    }

    update(contact) {
        const body: any = {};
        body.contact = JSON.stringify(contact);
        return this.apiService.call('/api/contacts/update', body);
    }

    delete(id) {
        const body: any = {};
        body.id = id;
        return this.apiService.call('/api/contacts/delete', body);
    }

}



