import { Component, OnInit, Input } from '@angular/core';
import { trigger, style, transition, animate, keyframes, state } from '@angular/animations';
import { Thread } from '../thread';
import { ImageViewerService } from '../../../../shared/image-viewer/image-viewer.service';
import { ContactsService } from '../../../../shared/services/contacts.service';
import { GeneralService } from '../../../../shared/services/general.service';
import { AfterViewInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { Router } from '@angular/router';
import { UserService } from '../../../../shared/services/user.service';
import { Contact } from '../../../../contacts/contact';
import { WebsocketService } from '../../../../shared/services/websocket.service';

@Component({
    selector: 'app-thread-header',
    templateUrl: './thread-header.component.html',
    styleUrls: ['./thread-header.component.scss'],
    animations: [
        trigger('glowBox', [
            state('glow', style({
                opacity: 0
            })),
            state('normal', style({
                opacity: 0
            })),
            transition('normal => glow', animate(12000, keyframes([
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 }),
                style({ opacity: 1 }),
                style({ opacity: 0 })
            ])))
        ])
    ],
})
export class ThreadHeaderComponent implements OnInit {

    @Input('thread') thread: Thread;
    @Input('imageUrl') imageUrl;
    @Input('contact') contact: Contact;
    gravatarId;
    isGlowing = false;
    isContactFormVisible = false;
    contactName = '';
    isProgressbarVisible = false;
    savingContactName = false;

    constructor(
        private imageViewer: ImageViewerService,
        private contactsService: ContactsService,
        private generalService: GeneralService,
        private router: Router,
        private userService: UserService,
        private websocketService: WebsocketService) {
    }

    ngOnInit() {

        this.gravatarId = this.contact.email || undefined;

        this.registerContactEventListners();

        setTimeout(() => {
            // check if thread has isGlowing property
            if (this.thread.isGlowing) {
                this.isGlowing = true;
            }

        }, 1);

    }

    registerContactEventListners() {

        this.registerDeleteContactEventListener();
        this.registerUpdateContactEventListener();

    }

    registerDeleteContactEventListener() {
        this.websocketService.contacts.delete.subscribe((data: any) => {
            if (this.contact.id === data.id) {
                this.contact.name = 'Unknown';
                this.contact.id = null;
            }
            console.log('\nDelete event listner in thread-header.ts \n data:', data);

        });
    }

    registerUpdateContactEventListener() {
        this.websocketService.contacts.update.subscribe((contact: any) => {
            if (this.contact && this.contact.id === contact.id) {
                this.contact = contact;
            }

            console.log('\nUpdate CONTACT event listner in thread-header.ts \n data:', contact);

        });
    }

    registerUpdateMessageEventListener() {
        this.websocketService.messages.update.subscribe((contact: any) => {
            if (this.contact && this.contact.id === contact.id) {
                this.contact = contact;
            }

            console.log('\nUpdate MESSAGE event listner in thread-header.ts \n data:', contact);

        });
    }

    showImageViewer() {
        this.imageViewer.show(this.imageUrl);
    }

    showAddContactForm() {
        if (!this.contact.id) {
            this.isContactFormVisible = true;
        }
        this.isContactFormVisible = true;
    }

    hideAddContactForm() {
        this.isContactFormVisible = false;
    }

    resetContactForm() {
        this.contactName = '';
        this.hideAddContactForm();
    }

    saveContactName() {
        this.isProgressbarVisible = true;
        this.savingContactName = true;
        const name = this.contactName;
        const number = this.thread.theirnumber;

        this.contactsService.addContacts({ name, number }).subscribe((response: any) => {

            this.resetContactForm();
            this.generalService.showNotification('Contact is saved successfully.', 'Close');
            this.isProgressbarVisible = false;
            this.savingContactName = false;
        },
            () => {
                this.isProgressbarVisible = false;
                this.savingContactName = false;
            });

    }

    stopGlowing($event) {
        if ($event.fromState === 'normal' && $event.toState === 'glow') {
            this.isGlowing = false;
        }
    }

    openConversation() {
        this.router.navigateByUrl('/app/conversation/ournumber/' + this.thread.ournumber + '/theirnumber/' + this.thread.theirnumber);
    }

    openEditContact() {
        this.router.navigateByUrl('/app/contacts/' + this.contact.id);
    }

    getContactName() {
        if (!this.contact) {
            return 'M';
        }
        return this.contact.initials || this.contact.name || 'M';
    }

    isOurNumber() {
        return this.userService.user.numbers.indexOf(this.contact.number) >= 0;
    }

}



