import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { ContactsService } from '../../shared/services/contacts.service';
import { GeneralService } from "../../shared/services/general.service";
import { TagsService } from "../../shared/services/tags.service";
import { DialogComponent } from '../../shared/dialog/dialog.component';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef } from '@angular/material';
import { ContextMenuComponent } from '../../shared/context-menu/context-menu.component';
import { WebsocketService } from '../../shared/services/websocket.service';


@Component({
    selector: 'app-contact-card-layout',
    templateUrl: './contact-card-layout.component.html',
    styleUrls: ['./contact-card-layout.component.scss']
})

export class ContactCardLayoutComponent implements OnInit, OnDestroy {
    @Input('contact') contact;
    @Output('contactDeleted') contactDeleted = new EventEmitter();
    @ViewChild(ContextMenuComponent) ContexMenuComponent: ContextMenuComponent;
    dialogRef: MatDialogRef<DialogComponent>;
    contextMenuItems = [
        {title: 'Delete contact', icon: 'delete'}
    ];
    isProgressbarVisible = false
    clickedB: EventEmitter<any> = new EventEmitter<any>();
    formSuggestedTags = [];

    public createTagLoaderVisualisation: boolean = false;
    public removeTagLoaderVisualisation: boolean = false;
    public taglistView: boolean = false;
    public tagModel: any = {};
    public contactsUpdateSubscription;

    constructor(private ContactService: ContactsService,
                private generalService: GeneralService,
                private tagsService: TagsService,
                private dialog: MatDialog,
                private router: Router,
                private webSocketService: WebsocketService) {
    }

    ngOnInit() {

        this.contactsUpdateSubscription = this.webSocketService.contacts.update.subscribe((data: any) => {
            if(this.contact && this.contact.id == data.contact.id){
                const tags = [];
                data.contact.tags.forEach((tag)=>{
                    tags.push(tag.name);
                });
                data.contact.tags = tags;
                this.contact = data.contact;
            }
        });
    }

    ngOnDestroy(){
        this.contactsUpdateSubscription.unsubscribe();
    }

    addNewTag() {
        this.createTagLoaderVisualisation = true;
        this.tagModel.contactId = this.contact.id;
        this.ContactService.addTag(this.tagModel).subscribe((response: any) => {
            if (response.error == null) {
                if (response) {
                    this.generalService.showNotification('Tag is created successfully!', 'Close');
                    this.createTagLoaderVisualisation = false;
                    this.clickedB.emit(response);
                }

            }
            ;
            this.createTagLoaderVisualisation = false;
        });
    };

    removeTags(tagName, contactId) {
        this.removeTagLoaderVisualisation = true;
        this.ContactService.removeTags(tagName, contactId).subscribe((response: any) => {
            if (response.error == null) {
                if (response) {
                    this.removeTagLoaderVisualisation = false;
                    this.clickedB.emit(response);
                    this.generalService.showNotification('Tag is removed successfully!', 'Close');
                }
            }
            ;
            this.removeTagLoaderVisualisation = false;
        });
    }

    expandContact() {

        this.taglistView = true;

    }

    searchTags() {
        this.tagsService.getTags({contains: this.tagModel.name}).subscribe((response: any) => {
            this.formSuggestedTags = response.results;
        });
    }

    showContextMenu(event) {
        this.ContexMenuComponent.showContextMenu(event);
    }

    onContextMenuBtnClicked(menuItem) {
        if (menuItem.title === 'Delete contact') {
            this.dialogRef = this.dialog.open(DialogComponent, {
                data: {
                    title: 'Delete contact',
                    text: 'Are you sure you want to permanently delete this contact?',
                    footerBtns: [
                        {label: 'Yes', color: 'primary', onCloseReturnData: {yes: 1}},
                        {label: 'No', color: 'default', onCloseReturnData: {yes: 0}},
                    ]
                }
            });
        }

        setTimeout(() => {
            this.registerDialogEvent();
        }, 100);

    }

    registerDialogEvent() {
        this.dialogRef.afterClosed().subscribe((response: any) => {
            if (response.yes === 1) {

                this.delete();

            }
        });
    }

    delete() {
        this.isProgressbarVisible = true;
        this.ContactService.delete(this.contact.id).subscribe((r: any) => {
            if (r.success) {
                this.generalService.showNotification('Contact deleted successfully.', 'Okay');
                this.contactDeleted.emit(this.contact.id);
                this.isProgressbarVisible = false;
            }
        }, () => {
            this.isProgressbarVisible = false;
        }, () => {
        });
    }


}



