import { Injectable, EventEmitter } from '@angular/core';
import { User } from '../interfaces/user';
import 'rxjs/add/operator/share';
import { Router } from '@angular/router';
import { MessageService } from './message.service'
import { ApiService } from './api.service';
import { WebsocketService } from './websocket.service';
import { GeneralService } from './general.service';


@Injectable()
export class UserService {

    user: User = null;
    accountUsers = [];

    constructor(
        private apiService: ApiService,
        private router: Router,
        private messageService: MessageService,
        private webSocketService: WebsocketService,
        private generalService: GeneralService
    ) {
        const u = localStorage.getItem('user');
        if (u) {
            this.start(JSON.parse(u))
        }
        this.apiService.call('/api/getUsers', { usedFor: 'thread.component' })
            .subscribe((response) => {
                if (response.error == null) {
                    this.accountUsers = response.results;
                }
            });

        this.webSocketService.contacts.update.subscribe((data) => {

            if(data.contact.account != this.user.accountId){
                return false;
            }
            const numberIndex = this.generalService.getObjectIndexFromArrayByKeyValue(this.user.numbers, 'number', data.contact.number);

            if(numberIndex>=0){
                this.user.numbers[numberIndex].contactName = data.contact.name;
                this.user.numbers[numberIndex].contactId = data.contact.contactId;
                window.localStorage.setItem('user',JSON.stringify(this.user));
            }

        });
    }


    linkToOtherServices() {
        this.apiService.userService = this
        this.messageService.start(this); //just added
    }

    logInEvent = new EventEmitter();

    loggedIn() {
        return this.user != null;
    }

    reset() {
        this.user = null;
        window.localStorage.removeItem('user');
    }


    start(user) {
        this.user = user;
        // storing user in localStorage to remember session
        window.localStorage.setItem('user', JSON.stringify(this.user));
        this.messageService.start(this);
        this.apiService.userService = this
    }


    logout() {
        this.reset();
        this.router.navigateByUrl('/login');
    }

    getUsers(options = {}) {
        return this.apiService.call('/api/getUsers', options)
    }

    signUp(data) {
        let body: any = {};
        body.email = data.email;
        body.password = data.password;
        body.company = data.company;
        return this.apiService.call('/api/addAccounts', body)
    }


    addUser(email, password, roleId, account_id, name) {
        let body: any = {};
        body.email = email;
        body.password = password;
        body.account_id = account_id;
        body.role_id = roleId;
        body.name = name;
        return this.apiService.call('/api/AddUsers', body)
    };

    completed_functions = {
        stripe_completed: () => {
            return this.user && this.user['stripeId'] && this.user['stripeId'].length > 0
        }
    }
}



