import { Component, Input, OnInit } from '@angular/core';
import { ApiService } from '../shared/services/api.service';
import { ContactsService } from '../shared/services/contacts.service';
import { GeneralService } from '../shared/services/general.service';
import { WebsocketService } from '../shared/services/websocket.service';
@Component({
    selector: 'app-contacts',
    templateUrl: './contacts.component.html',
    styleUrls: ['./contacts.component.scss']
})

export class ContactsComponent implements OnInit {

    public contactList: any[] = [];
    public searchKeyword: string = '';
    public contactLoaderVisualization: boolean = true;
    constructor(
        private contactsService: ContactsService,
        private generalService: GeneralService,
        private webSocketService: WebsocketService
    ) {
    }

    ngOnInit() {
        this.getContacts();
        this.webSocketService.contacts.delete.subscribe((data: any) => {
            this.contactDeleted(data.id);
        });

        this.webSocketService.contacts.create.subscribe((data: any) => {
            this.contactList.unshift(data);
        });


    }



    getContacts() {
        this.contactsService.getContacts({contains: this.searchKeyword}).subscribe((response: any) => {
            if (response.error == null) {
                this.contactList = response.results;
                console.log('contacts :', response.results);
            };
            this.contactLoaderVisualization = false;
        });
    };

    /* for event emitter */
    getContacts2(event) {
        this.getContacts();
    }

    contactDeleted(contactId) {
        this.generalService.removeObjectFromArrayByKeyValue(this.contactList, 'id', contactId);
    }



}



