import {
    AfterContentInit,
    Component,
    ElementRef,
    EventEmitter, HostListener, Input, OnInit, Output,
    ViewChild
} from '@angular/core';
import { MatMenuTrigger } from '@angular/material';
import * as jQuery from 'jquery';
import { MessageService } from '../../shared/services/message.service';
import { GeneralService } from '../../shared/services/general.service';
@Component({
  selector: 'app-number-threads-context-menu',
  templateUrl: './number-threads-context-menu.component.html',
  styleUrls: ['./number-threads-context-menu.component.scss']
})
export class NumberThreadsContextMenuComponent implements OnInit, AfterContentInit {

    @Input('autoDetectZone') autoDetectZone;
    @Input('number') number;
    @Input('assigned_only') assigned_only;
    @ViewChild(MatMenuTrigger) trigger: MatMenuTrigger;
    @ViewChild('triggerBtn') triggerBtn: ElementRef;
    @HostListener('contextmenu', ['$event']) onContextMenu($event) {this.showContextMenu($event); }
    @Output('onAssignUserClicked') onAssignUserClicked = new EventEmitter<any>();

    yPosition = 0;
    xPosition = 0;

    constructor(private el: ElementRef,
                private messageService: MessageService,
                private generalService: GeneralService
    ) {

    }
    ngAfterContentInit(){
        if(this.autoDetectZone !== undefined && this.autoDetectZone){
            jQuery(this.el.nativeElement).css({position: 'absolute', top: 0, left: 0, display: 'block', width: '100%', height: '100%'});
        }

    }
  ngOnInit() {

  }

    showContextMenu($event: Event) {

        jQuery(document).off('contextmenu');
        jQuery(document).on('contextmenu', (e) => {
            e.preventDefault();
            this.hideContextMenu();
        });

        $event.stopImmediatePropagation();
        $event.preventDefault();
        this.yPosition = $event['pageY'] - jQuery(document).scrollTop();
        this.xPosition = $event['pageX'];
        jQuery(this.triggerBtn.nativeElement).trigger('click');
        this.trigger.closeMenu();
        return false;

    }

    hideContextMenu() {
        this.trigger.closeMenu();
        jQuery(document).off('contextmenu');

    }


    createNotifications(type){

        const config: any = {};
        config.ournumber = this.number;
        config.type = type;
        config.assigned_only = this.assigned_only;
        this.messageService.subscribeNotifications(config).subscribe((response: any)=>{
            if(response.success==true){
                this.generalService.showNotification('You have been subscribed with '+type+' notifications.', 'Okay');
            }
        });
    }



}



