import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MessageService } from '../shared/services/message.service';
import { Message } from './message/message';
import { ApiService } from '../shared/services/api.service';
import { WebsocketService } from '../shared/services/websocket.service'
import { Array } from 'core-js/library/web/timers';
import { GeneralService } from '../shared/services/general.service';

@Component({
    selector: 'app-conversation',
    templateUrl: './conversation.component.html',
    styleUrls: ['./conversation.component.scss'],
})
export class ConversationComponent implements OnInit {

    @Input('ournumber') ournumber;
    @Input('theirnumber') theirnumber;
    @Output('conversationLoadStart') conversationLoadStart: EventEmitter<any> = new EventEmitter();
    @Output('conversationLoaded') conversationLoaded: EventEmitter<any> = new EventEmitter();
    messages: Message[] = [];
    contacts: any = [];
    loadingConversation = false;

    messageLimit: number = 100
    constructor(
        private messageService: MessageService,
        private apiService: ApiService,
        private webSocketService: WebsocketService,
        private generalService: GeneralService
    ) { }




    ngOnInit() {
        this.loadConversation(resp => {
            this.contacts = resp.contacts;
            this.messages = resp.messages.reverse();
        })

        this.webSocketService.messages.delete.subscribe((data: any) => {
            this.generalService.removeObjectFromArrayByKeyValue(this.messages, 'id', data.id);
        });
        this.webSocketService.messages.create.subscribe(msg => {
            let m = this.messageService

            if (msg.ournumber != this.ournumber) return //this message is not for the this conversation

            if (m.messageExistsById(msg, this.messages)) { //This is either a duplicate or an update
                var match = m.returnMsgByMatchingId(msg, this.messages) //find the match
                if (m.messageIsNewerThanMessage(msg, match)) {//if it is newer
                    m.replaceMessageInArray(msg, match, this.messages)
                } // replace the old one
                else return // do nothing because the matching message is older (or == in the case of a duplicate)
            } else { //this message must belong here
                m.insertMessageByDate(msg, this.messages, this.messageLimit) //insert it into the array in the right place
            }
        })
    }

    loadConversation(callback) {
        this.conversationLoadStart.emit();
        this.loadingConversation = true;
        this.apiService.call('/components/conversation-component', { ournumber: this.ournumber, theirnumber: this.theirnumber }).subscribe((response: any) => {
            this.loadingConversation = false;
            this.conversationLoaded.emit();
            callback(response);
        }, () => {
            this.loadingConversation = false;
        })

    }

    getState(date) {
        let diff = (new Date().getTime() - new Date(date).getTime()) / 1000;
        return (parseInt(diff.toString()) < 10) ? 'flash' : 'none';
    }
}



