import { Component, Input } from '@angular/core';
import { UserService } from '../../../shared/services/user.service';

@Component({
  selector: 'my-app-sidenav-menu',
  styles: [],
  templateUrl: './sidenav-menu.component.html'
})

export class AppSidenavMenuComponent {
    numbers = [];
    constructor(
        private userService: UserService
    ) {
        this.numbers = this.userService.user.numbers;
    }

    isUserAdmin(){
        return this.userService.user.role == "Administrator";
    }

    hasNeedlesAccess(){
        return this.userService.user.Needles_Integrated == 1;
    }
}



