import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ThreadHeaderComponent, ThreadFooterComponent, ThreadBodyComponent, ThreadComponent } from './thread';
import { SharedModule } from '../../shared/shared.module';
import { RouterModule } from '@angular/router';

import { MessageFormFloatingButtonsModule } from '../../shared/message-form-floating-buttons/message-form-floating-buttons.module';
import { ThreadContextMenuComponent } from './thread/thread-context-menu/thread-context-menu.component';
import { NewMessageWindowModule } from '../../new-message-window/new-message-window.module';
import { ThreadComposeComponent } from './thread/compose/thread-compose.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule,
    MessageFormFloatingButtonsModule,
    NewMessageWindowModule
  ],
  declarations: [
    ThreadFooterComponent,
    ThreadContextMenuComponent,
    ThreadHeaderComponent,
    ThreadBodyComponent,
    ThreadComponent,
    ThreadComposeComponent,
  ],
  exports: [ThreadComponent]
})
export class ThreadModule { }



