import { Injectable, EventEmitter } from '@angular/core';

@Injectable()
export class TimerService {

  constructor() { }

  public tenSecondTimer = new EventEmitter<Date>();

  public oneSecondTimer = new EventEmitter<Date>()

  private tenSecondTimerTimer = setInterval(() => {
    this.tenSecondTimer.emit(new Date())
  }, 10000)

  private oneSecondTimerTimer = setInterval(() => {
    this.oneSecondTimer.emit(new Date())
  }, 1000)


}



