import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Message } from '../../conversation/message/message';
import { MessageService, SendMessageConfigs } from '../../shared/services/message.service';
import { GeneralService } from '../../shared/services/general.service';
import { MessageFormFloatingButtonsComponent } from "../../shared/message-form-floating-buttons/message-form-floating-buttons/message-form-floating-buttons.component";

@Component({
  selector: 'app-send-message-form',
  templateUrl: './send-message-form.component.html',
  styleUrls: ['./send-message-form.component.scss']
})
export class SendMessageFormComponent implements OnInit {
  @Input('from') from;
  @Input('to') to;
  @Output('sent') sent: EventEmitter<any> = new EventEmitter();
  @Output('cancel') cancelEmitter: EventEmitter<any> = new EventEmitter();
  @Output('onFromShow') onFromShow: EventEmitter<any> = new EventEmitter();
  @ViewChild(MessageFormFloatingButtonsComponent) MessageFormFloatingButtonsComponent;
  isFormActive = false;
  msg: string = '';
  scheduledTime = '';
  fileId = '';
  googleAccessToken = '';
  selectingImage = false;
  selectingSchedule = false;
  constructor(
    private messageService: MessageService,
    private generalService: GeneralService
  ) { }

  ngOnInit() {

  }

  sendMsg() {

    const configs: SendMessageConfigs = { to: this.to, from: this.from, body: this.msg, scheduled_time: this.scheduledTime, fileId: this.fileId, googleAccessToken: this.googleAccessToken };

    this.msg = '';

    this.messageService.sendMessage(configs).subscribe((response: any) => {

      this.reset();
    });
  }

  cancel() {
    this.reset();
  }

  reset() {
    this.msg = '';
    this.cancelEmitter.emit();
    this.scheduledTime = '';
    this.googleAccessToken = '';
    this.fileId = '';
    this.MessageFormFloatingButtonsComponent.reset();
  }

  showForm() {
    this.isFormActive = true;
    this.onFromShow.emit();
  }

  hideForm() {
    if (this.selectingImage || this.selectingSchedule) return false;
    this.isFormActive = false;
  }
  shouldDisableSubmitBtn() {
    if (this.msg.length < 1) {
      return true;
    }
    return false;
  }
  onPhotoSelectionStart() {
    this.selectingImage = true;
  }

  onPhotoSelectionEnd(response) {
    this.selectingImage = false;
    if (response.googleAccessToken) {
      this.googleAccessToken = response.googleAccessToken;
      this.fileId = response.fileId;
    }

  }

  onScheduleSelectionStart() {
    this.selectingSchedule = true;
  }

  onScheduleSelectionEnd(scheduleTime) {
    this.selectingSchedule = false;
    this.scheduledTime = scheduleTime;
  }

  onDiscard() {
    this.reset();
  }

  removeTimer() {
    this.scheduledTime = '';
    this.MessageFormFloatingButtonsComponent.resetSubmitIcon();
  }

}



