import { RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './layout.component';
import { ShowNumberThreadsComponent } from '../show-number-threads/show-number-threads.component';
import { MessagesComponent } from '../messages/messages.component';
import { ContactsComponent } from '../contacts/contacts.component';
import { StripeComponent } from '../stripe/stripe.component';
import { ConversationPageComponent } from '../conversation-page/conversation-page.component';
import { ContactComponent } from '../contacts/contact/contact.component';
import { NotificationsComponent } from '../notifications/notifications.component';
import { AdminGuard } from '../admin/admin.guard';
import { NeedlesGuard } from '../needles/needles.guard';

const routes: Routes = [
    {
        path: 'app',
        component: LayoutComponent,
        children: [
            {path: '', redirectTo: '/app/messages', pathMatch: 'full'},
            {path: 'number/:number', component: ShowNumberThreadsComponent},
            {path: 'number/:number/page/:page', component: ShowNumberThreadsComponent},
            {path: 'conversation/ournumber/:ournumber/theirnumber/:theirnumber', component: ConversationPageComponent},
            {path: 'messages', component: MessagesComponent},
            {
                path: 'messages/scheduled',
                loadChildren: '../scheduled-messages/scheduled-messages.module#ScheduledMessagesModule'
            },
            {path: 'admin', loadChildren: '../admin/admin.module#AdminModule', canActivate:[AdminGuard]},
            {path: 'needles', loadChildren: '../needles/needles.module#NeedlesModule', canActivate:[NeedlesGuard]},
            {path: 'contacts/:id', component: ContactComponent},
            { path: 'contacts', component: ContactsComponent },
            {path: 'notifications', component: NotificationsComponent},
            {path: 'settings', loadChildren: '../settings/settings.module#SettingsModule'}
        ]
    }
];

export const LayoutRoutingModule = RouterModule.forChild(routes);



