import { Component, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../../../../shared/services/user.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MessageService, SendMessageConfigs } from '../../../../shared/services/message.service';
import { GeneralService } from '../../../../shared/services/general.service';
import { MessageFormFloatingButtonsComponent } from '../../../../shared/message-form-floating-buttons/message-form-floating-buttons/message-form-floating-buttons.component';
import { Tag } from '../../../../contacts/tag';
import { ApiService } from '../../../../shared/services/api.service';
import { Contact } from '../../../../contacts/contact';
import { MatAutocomplete } from '@angular/material';
import { WebsocketService } from '../../../../shared/services/websocket.service';

@Component({
    selector: 'thread-compose-window',
    templateUrl: './thread-compose.component.html',
    styleUrls: ['./thread-compose.component.scss']
})
export class ThreadComposeComponent implements OnInit {
    tag: Tag;
    tags: Tag[] = [];
    contacts: Contact[] = [];
    accountNumbers = this.userService.user.numbers;
    minimized = false;
    closed = true;
    formGroup: FormGroup;
    btnDisabled = false;
    processing = false;
    openedThroughClick = false;
    selectingNumber = false;
    selectingDateTime = false;
    isMouseOverMainBtn = false;
    windowContainerZIndex = -11111111;
    selectingImage = false;
    discarding = false; // to avoid from opening if user click on discard icon
    @ViewChild(MessageFormFloatingButtonsComponent) MessageFormFloatingButtonsComponent;
    constructor(
        private userService: UserService,
        private fb: FormBuilder,
        private messageService: MessageService,
        private generalService: GeneralService,
        private apiService: ApiService,
        private webSocketService: WebsocketService
    ) { }

    ngOnInit() {
        this.setupForm();
        this.registerEvents();
    }

    registerEvents() {
        this.messageService.onNewMessageWindowShow.subscribe((data) => {
            this.open();
            if (data.useAcountNumberObj) {
                this.formGroup.patchValue({ from: data.useAcountNumberObj.number });
                this.formGroup.updateValueAndValidity();

            }

        });
    }

    setupForm() {

        const controls: any = {};
        controls.from = this.fb.control('', Validators.required);
        controls.to = this.fb.control('', Validators.required);
        controls.message = this.fb.control('');
        controls.fileId = this.fb.control('');
        controls.googleAccessToken = this.fb.control('');
        controls.scheduled_time = this.fb.control('');

        this.formGroup = this.fb.group(controls);

    }

    minimize() {
        if (this.minimized === false) {
            this.minimized = true;
        } else {
            this.minimized = false;
        }

    }

    onSelectingNumber(opened) {
        if (opened) {
            this.selectingNumber = true;
        }
    }

    close(action = '') {

        if (this.selectingImage || this.selectingDateTime) {
            return false;
        }
        if (action === 'discard') {
            this.discarding = true;
        }

        setTimeout(() => {
            // don't auto close on mouse out if opened through external mouse click
            if ((this.openedThroughClick && action === 'mouseOutFromWindowContainer') ||
                this.selectingNumber === true ||
                this.isMouseOverMainBtn === true
            ) {
                return false;
            }

            this.closed = true;

            setTimeout(() => {
                //  reset only if form is empty
                if (
                    (
                        (this.formGroup.value.scheduled_time === null) &&
                        this.formGroup.value.fileId.length === 0 &&
                        this.formGroup.value.from.length === 0 &&
                        this.formGroup.value.message === 0 &&
                        this.formGroup.value.to.length === 0) ||
                    action === 'msgSent'
                    || action === 'discard'
                ) {
                    this.reset();
                }
            }, 1400);
        }, 199);

        setTimeout(() => {
            this.windowContainerZIndex = -111111;
            this.discarding = false;
        }, 1700);
    }

    open(action = '', eventName = '') {

        if (this.processing === true || this.discarding === true || (action === 'mouseOverFromWindowContainer' && this.closed === true) || (this.selectingNumber === true)) {
            return false;
        }
        this.windowContainerZIndex = 111;
        this.closed = false;
        // this.reset();
    }

    reset() {
        this.formGroup.reset();
        this.openedThroughClick = false;
        this.selectingNumber = false;
        this.MessageFormFloatingButtonsComponent.reset();
        this.removeTimer();
        this.tag = null;
        this.minimized = false;
    }

    send() {
        this.btnDisabled = true;
        this.processing = true;

        const configs: SendMessageConfigs = {
            to: this.formGroup.value.to,
            from: this.formGroup.value.from,
            body: this.formGroup.value.message,
            fileId: this.formGroup.value.fileId,
            googleAccessToken: this.formGroup.value.googleAccessToken,
            scheduled_time: this.formGroup.value.scheduled_time
        };

        if (this.tag) {
            configs.tagId = this.tag.id;
        }

        this.messageService.sendMessage(configs).subscribe((response: any) => {
            this.btnDisabled = false;
            this.processing = false;
            this.close('discard');
        });

    }

    shouldBtnEnable() {

        if (!this.btnDisabled && this.formGroup.valid) {
            return true;
        }

        return false;

    }

    submitBtnLabel() {

        if (this.processing) {
            return 'Please wait...';
        }
        return 'Send';

    }

    afterSelectingNumber() {
        setTimeout(() => {
            this.selectingNumber = false;
        }, 200);
    }

    removeTimer() {
        this.formGroup.patchValue({ scheduled_time: '' });
        this.formGroup.updateValueAndValidity();
        this.MessageFormFloatingButtonsComponent.resetSubmitIcon();
    }

    onPhotoSelectionStart() {
        this.selectingImage = true;
    }

    onPhotoSelectionEnd(response) {
        this.formGroup.patchValue({ fileId: response.fileId, googleAccessToken: response.googleAccessToken });
        this.formGroup.updateValueAndValidity();
        this.selectingImage = false;
    }
    onScheduleSelectionStart() {
        this.selectingDateTime = true;
    }
    onScheduleSelectionEnd(scheduledTime) {
        this.formGroup.patchValue({ scheduled_time: scheduledTime });
        this.formGroup.updateValueAndValidity();
        this.selectingDateTime = false;
    }
    onSubmit() {
        this.send();
    }

    searchContact() {
        this.apiService.call('/components/new-message-window', { contains: this.formGroup.value.to }).subscribe((response: any) => {
            if (!response.error) {
                this.contacts = response.contacts;
                this.tags = response.tags;
            }
        });
    }

    tagSelected(tag) {
        this.tag = tag;
        this.formGroup.patchValue({ to: tag.id });
        this.formGroup.updateValueAndValidity();
    }

    onMainBtnClicked() {
        if (this.closed) {
            this.open();
        } else {
            if (!this.minimized) {
                this.close('discard');
            } else {
                this.minimized = false;
            }

        }
    }
}



