import { Component, OnInit } from '@angular/core';
import { UserService } from '../shared/services/user.service';
import { Router } from '@angular/router';


@Component({
    selector: 'app-sign-up',
    templateUrl: './sign-up.component.html',
    styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {

    constructor(
        private userService: UserService,
        private router: Router
    ) { }

    ngOnInit() {
    }

    signup = {
      email: undefined,
      password: undefined,
      password2: undefined,
      name: undefined ,
      company: undefined,
      agree: undefined
    };


    signUp() {
        if (!this.validateEmail()) {
          alert('The email is invalid')
          return
        };

        if (!this.isValid(this.signup.company)) {
          alert('Firm is required and must be more than 4 characters.')
          return
        }
        if (!this.isValid(this.signup.password)) {
          alert('Password is required and must be more than 4 characters.')
          return
        }
        if (!this.passwordsMatch()) {
          alert('Passwords do not match.')
          return
        }
        if (!this.signup.agree) {
          alert('You must agree to the Terms and Conditions')
          return
        }

        this.userService.signUp(this.signup).subscribe((response: any ) => {
            if (response.success === true) {
                this.router.navigateByUrl('/');
            }
        })

    }

    validate(t) {
        return t ? "/assets/images/signupForm/check-clipart-6.png" : "/assets/images/signupForm/xmark.png"
    }
    isValid(p) {
        return p && p.length > 4
    }
    validateEmail() {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return this.signup.email && re.test(this.signup.email);
    }
    passwordsMatch() {
        return this.signup.password == this.signup.password2 && this.isValid(this.signup.password)
    }

}



